.faq{
    &__item{
        border-bottom: 1px solid rgba($color__black, .2);
        &:first-child{
            border-top: 1px solid rgba($color__black, .2);
        }
    }
    &__question{
        display: flex;
        align-items: center;
        padding: 30px;
        cursor: pointer;
        margin: 0;
        transition: all 250ms ease;
        position: relative;
        @include media-breakpoint-down(sm){
            font-size: $fontSize__small-plus;
            padding: 20px 10px;
            justify-content: flex-start !important;
        }
        &:after{
            content: "+";
            font-weight: $fontWeight__bold;
            font-size: $fontSize__medium;
            display: block;
            position: absolute;
            top: 50%;
            right: 30px;
            transform: translateY(-50%);
            @include media-breakpoint-down(sm){
                right: 10px;
            }
        }
        &:hover{
            padding-left: 40px;
            @include media-breakpoint-down(sm){
                padding-left: 10px;
                color: $color__secondary;
            }
        }
    }
    .active{
        &:after{
            content: "-";
        }
    }
    &__answer{
        display: none;
        padding: 0 30px;
        max-width: 680px;
        @include media-breakpoint-down(sm){
            padding: 0 10px;
            text-align: left;
        }
    }
    &--center{
        text-align: center;
        .faq__question{
            justify-content: center;
        }
        .faq__answer{
            margin: 0 auto;
        }
    }
}