.compte__menu {
    @extend .list--reset;
    border-top: 1px solid $color__grey--light;

    li {
        a {
            display: block;
            padding: 10px 10px;
            border-bottom: 1px solid $color__grey--light;
            @extend .text--highlight;
            opacity: 1;
            transition: all 500ms ease;

            &:hover {
                background-color: $color__grey--extra-light;
            }

            &.active {
                font-weight: $fontWeight__bold;
            }
        }
    }
}

.compte__commandes {
    thead {
        border-bottom: 1px solid $color__secondary;

        tr {
            th {
                text-transform: uppercase;
                padding: 5px 10px;
            }
        }
    }

    tbody {
        tr {
            &:nth-child(even) {
                background-color: $color__grey--extra-light;
            }

            td {
                padding: 15px 10px;

                &.ref {
                    a {
                        color: $color__secondary;
                        text-decoration: underline;
                    }
                }
                &.status{
                    .status{
                        display: flex;
                        align-items: center;
                        &:before{
                            content: "";
                            width: 10px;
                            height: 10px;
                            display: inline-block;
                            background-color: $color__grey--light;
                            border-radius: 50%;
                            margin-right: 8px;
                        }
                        &--completed:before{ background-color: rgb(24, 216, 33); } 
                        &--pendind:before{ background-color: rgb(199, 196, 25); }
                        &--failed:before{ background-color: rgb(197, 28, 28); }
                        &--processing:before{ background-color: rgb(27, 119, 155); }
                        &--cancelled:before{ background-color: rgb(32, 15, 15); }
                    }
                }
                &.invoice {
                    a {
                        i {
                            font-size: 3rem;
                            color: $color__secondary;
                        }

                        &:hover {
                            i {
                                color: $color__primary;
                            }
                        }
                    }
                }
            }
        }
    }
}