.hidden{
    display: none !important;
}
.hidden--mobile{
    @include media-breakpoint-down(sm){
        display: none !important;
    }
}
.show--mobile{
    display: none !important;
    @include media-breakpoint-down(sm){
        display: block !important;
    }
}
.bloc--text-content{
    background-color: $color__white;
    padding: 13px 10px 23px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
}

.col--space-between{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.picture--fluid{
    img{
        width: 100%;
        height: auto;
    }
}
.img--border{
    img{
        border: 1px solid $color__primary;
        display: block;
    }
}

.flex{
    display: flex;
}
.flex--center{
    @extend .flex;
    flex-direction: column;
    align-items: center;
}
.align-item--center{
    align-items: center;
}

.vertical-columns{
   column-count: 3; 
   grid-column-gap: 0;
   width: 100%;
   & > *{
       break-inside: avoid-column;
   }
   @include media-breakpoint-down(sm){
        column-count: 2;
   }
   @include media-breakpoint-down(xs){
    column-count: 1;
    }
}
.vertical-columns--para-wrapper{
    padding: 0;
    p{
        padding: 0 15px;
    }
}
.columns-count-2{
    column-count: 2;
    @include media-breakpoint-down(xs){
     column-count: 1;
    }
}
.columns-count-4{
    column-count: 4;
    @include media-breakpoint-down(sm){
         column-count: 2;
    }
    @include media-breakpoint-down(xs){
     column-count: 1;
     }
}
.columns-gap{
    grid-column-gap: 30px;
}

.col-shifted{
    margin-top: 62px;
    @include media-breakpoint-down(sm){
        margin-top: 0;
    }
}

.col-fullsize--sm{
    @include media-breakpoint-down(sm){
        .col-12, &.col-12{
            padding-left: 0;
            padding-right: 0;
        }
    }
}