.icon{
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    transition: all 250ms ease;
    color: $color__primary;
    &:hover{
        color: $color__secondary;
    }
    &.icon--round{
        background-color: $color__grey;
        border-radius: 50%;
        color: $color__secondary;
        &:hover{
            background-color: $color__primary;
            color: $color__white;
        }
        &.icon--primary{
            background-color: $color__primary;
            color: $color__white;
            &:hover{
                background-color: $color__secondary;
            }
        }
    }
}