@font-face {
	font-family: "sothys";
	src: url('../fonts/sothys.eot');
	src: url('../fonts/sothys.eot?#iefix') format('eot'),
		url('../fonts/sothys.woff2') format('woff2'),
		url('../fonts/sothys.woff') format('woff'),
		url('../fonts/sothys.ttf') format('truetype'),
		url('../fonts/sothys.svg#sothys') format('svg');
}

@mixin icon-styles {
	font-family: "sothys";
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
	font-style: normal;
	font-variant: normal;
	font-weight: normal;
	// speak: none; // only necessary if not using the private unicode range (firstGlyph option)
	text-decoration: none;
	text-transform: none;
}

%icon {
	@include icon-styles;
}

