.checkout-steps{
    @extend .list--horizontal;
    display: flex;
    justify-content: space-between;
    position: relative;
    margin-bottom: 26px !important;
    @include media-breakpoint-down(sm){
        margin-bottom: 10px !important;
    }
    &:before,
    &:after{
        content: "";
        position: absolute;
        top: 20px;
        left: 60px;
        height: 1px;
        width: calc( 100% - 100px );
        background-color: $color__grey--light;
        z-index: -1;
        @include media-breakpoint-down(sm){
            width: calc( 100% - 40px );
            top: 13px;
            left: 20px;
        }
    }
    &:after{
        background-color: $color__primary;
    }
    li,a{
        font-family: $font__secondary;
        font-weight: $fontWeight__default;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        strong{
            background-color: $color__grey--extra-light2;
            border-radius: 50%;
            width: 40px;
            height: 40px;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: $fontSize__big;
            color: $color__white;
            padding-top: 2px;
            @include media-breakpoint-down(sm){
                font-size: $fontSize__extra-extra-small;
                width: 26px;
                height: 26px;
            }
        }
        span{
            padding-top: 5px;
            font-size: $fontSize__big;
            line-height: $fontSize__big + .6rem;
            color: $color__secondary;
            transition: color 250ms ease;
            span{
                padding: 0;
            }
            @include media-breakpoint-down(sm){
                font-size: $fontSize__default;
                line-height: $fontSize__default + .6rem;
            }
        }
        &.active{
            color: $color__primary;
        }
        &.active,
        &.done{
            strong{
                background-color: $color__primary;
            }
        }
        
    }
    a:hover{
        span{
            color: $color__primary;
        }
    }
    &--1:after{
        width: calc( 0% );
    }
    &--2:after{
        width: calc( 33.333333333% - 20px );
    }
    &--3:after{
        width: calc( 66.666666666% - 60px );
    }
    &--4:after{
        width: calc( 100% - 120px );
        @include media-breakpoint-down(sm){
            width: calc( 100% - 50px );
        }
    }
}

.checkout--purchases{
    margin-top: 60px;
    border-top: 1px solid $color__grey--light;
    padding-top: 20px;
    margin-bottom: 57px;
    @include media-breakpoint-down(sm){
        margin-top: 0;
        margin-bottom: 20px;
    }
    a{
        @extend .link--back;
        @include media-breakpoint-down(sm){
            justify-content: center;
            font-size: $fontSize__default--mobile;
        }
    }
}