.carousel{
  display: block;
  .card.card--product{
    padding: 0 20px;
    @include media-breakpoint-down(sm){
      padding: 0;
    }
  }
  &--nav{
    padding: 0 100px;
    position: relative;
    @include media-breakpoint-down(sm){
      padding: 0;
    }
    .carousel__nav{
      a{
        position: absolute;
        top: 0;
        width: 30px;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 4.4rem;
        cursor: pointer;
        @include media-breakpoint-up(md){
          width: 100px;
        }
        i{
          transition: transform 250ms ease;
          font-size: 44px;
          color: $color__secondary;
        }
        &.prev{
          left: 0;
        }
        &.next{
          right: 0;
        }
        @include media-breakpoint-up(lg){
          &:hover{
            &.prev i{
              transform: translateX(-10px) scale(1.2);
            }
            &.next i{
              transform: translateX(10px) scale(1.2);
            }
          }
        }
        &:active{
          &.prev i{
            transform: translateX(0px) scale(1);
          }
          &.next i{
            transform: translateX(0px) scale(1);
          }
        }
      }
    }
    &-desktop{
      @include media-breakpoint-down(xs){
        .carousel__nav{
          display: none;
        }
      }
    }
  }
  &--pagination{
    .carousel__pagination{
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      padding: 15px 0;
      @include media-breakpoint-down(md){
        padding-top: 0;
      }
      a{
        margin: 5px 10px;
        background-color: #D7D9DA;
        width: 30px;
        height: 4px;
        &:hover, &.active{
          background-color: $color__primary;
        }
        span{
          display: none;
        }
      }
    }
    &-mobile{
      @include media-breakpoint-up(md){
        .carousel__pagination{
          display: none;
        }
      }
    }
  }
  &__item{
    img{
      width: 100%;
    }
  }
}