.block{
    .block__visuel{
        margin-bottom: 10px;
        img, picture{
            width: 100%;
            height: auto;
        }
    }
    .block__content{
        p{
            color: $color__secondary;
        }
        .button{
            margin-top: 35px;
        }
    }
    &:hover{
        .button{
            @extend .button:hover;
        }
    }
    &--idees-cadeaux{

    }
    &--reassurance-vente{
        text-align: center;
        padding: 0 15px;
        display: block;
        @include media-breakpoint-down(sm){
            padding: 0;
        }
        .block__visuel{
            display: flex;
            justify-content: center;
            position: relative;
            padding-top: 43px;
            padding-bottom: 20px;
            margin: 0;
            @include media-breakpoint-down(sm){
                padding-top: 0;
            }
            &:after{
                content: "";
                width: 42px;
                height: 1px;
                display: block;
                background-color: $color__primary;
                position: absolute;
                bottom: 0;
                left: 50%;
                transform: translateX(-50%);
                transition: $transition-base;
            }
            .icon{
                font-size: 74px;
                height: 74px;
                width: auto;
                @include media-breakpoint-down(sm){
                    font-size: 64px;
                    height: 64px;
                }
            }
        }
        .block__content{
            padding: 20px 0 42px;
            .block__title, &>b, &>strong{
                font-size: $fontSize__medium;
                font-weight: $fontWeight__bold;
                color: $color__secondary;
                @include media-breakpoint-down(sm){
                    font-size: $fontSize__default--mobile;
                    line-height: $fontSize__default--mobile + .7rem;
                    display: block;
                }
            }
            p{
                margin: 0;
                @include media-breakpoint-down(sm){
                    font-size: $fontSize__default--mobile;
                    line-height: $fontSize__default--mobile + .7rem;
                }
            }
        }
    }
    a.block:hover{
        .block__visuel{
            .icon{
                color: $color__secondary;
            }
            &:after{
                width: 20px;
            }
        }
    }
    &--content-reverse{
        .block__content{
            display: flex;
            flex-direction: column-reverse;
        }
    }
    &--faq{
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        margin-bottom: 30px;
        border: 1px solid $color__primary;
        padding: 20px;
        min-height: 220px;
        align-items: flex-end;
        .block__content{
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            .icon{
                width: 8rem;
                height: 8rem;
                font-size: 8rem;
                display: flex;
            }
            span{
                @extend .title--big;
                position: relative;
                text-transform: uppercase;
                padding-top: 20px;
                margin-top: 20px;
                margin-bottom: 0;
                color: $color__secondary;
                font-size: $fontSize__small-plus;
                line-height: $fontSize__small-plus + .6rem;
                text-align: center;
                &:before{
                    content: "";
                    width: 40px;
                    height: 1px;
                    background-color: $color__primary;
                    position: absolute;
                    top: 0;
                    left: 50%;
                    transform: translateX(-50%);
                    transition: background 250ms ease;
                }
            }
        }
        &:hover{
            background-color: $color__primary;
            .icon, span{
                color: $color__white;
            }
            span{
                &:before{
                    background-color: $color__white;
                }
            }
        }
    }
    &--e-shop{
        border: 1px solid $color__secondary;
        text-align: center;
        padding: 40px;
        display: flex;
        height: 100%;
        color: $color__secondary;
        flex-direction: column;
        align-items: space-between;
        justify-content: space-between;
        @include media-breakpoint-down(md){
            padding: 40px 20px;
            margin: 10px 0;
            height: auto;
        }
    }
}