.resultat-prouve{
    text-align: center;
    margin-top: 50px;
    @include media-breakpoint-down(sm){
        margin-top: 20px;
    }
    .title{
        font-size: 4.7rem;
        line-height: 4rem;
        font-family: $font__secondary;
        margin-bottom: 10px;
        small{
            font-size: 3.2rem;
        }
        span{
            font-size: $fontSize__small-plus;
            line-height: $fontSize__small-plus + 0.8rem;
            margin-top: 5px;
            font-family: $font__main;
            display: block;
            letter-spacing: 0.93px;
            font-weight: $fontWeight__bold;
            text-transform: none;
        }
    }
    .text--legend{
        border-top: 1px solid #E1E1E1;
        padding-top: 10px;
        margin-top: 20px;
    }
    &--horizontal{
        display: flex;
        align-items: center;
        .title, p{
            margin: 0;
            // align-items: center;
            // display: flex;
            // flex-direction: column;
        }
        p{
            text-align: left;
            padding-left: 20px;
        }
    }
}