.lightbox{
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
    &__overlay,
    &__loader,
    &__content{
        position: fixed;
        left: 0;
        right: 0;
        bottom: 0;
        top: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
        transition: all 500ms ease 250ms;
        z-index: 10001;
    }
    &__overlay{
        background-color: rgba($color__black, .7);
    }
    &__loader{
        display: flex;
        justify-content: center;
        align-items: center;
        transition-delay: 500ms;
    }
    &__content{
        position: absolute;
        transform: scale(0.7);
        background-color: $color__white;
        .lightbox__inner{
            background-color: $color__white;
            .step{
                margin: 0 auto;
                display: block;
                @extend .text--highlight;
                opacity: 1;
                text-align: center;
                margin-bottom: -15px;  
            }
        }
    }
    &.show{
        z-index: 10000;
        .lightbox__overlay,
        .lightbox__loader{
            opacity: 1;
        }
    }
    &.loaded{
        .lightbox__loader{
            opacity: 0;
        }
        .lightbox__content{
            opacity: 1;
            transform: scale(1);
        }
    }
}