form{  
    fieldset{
        padding: 0;
        &.fieldset__margin{
            margin-top: 40px;
            @include media-breakpoint-down(sm){
                margin-top: 20px;
            }
        }
        & + fieldset{
            @include media-breakpoint-down(sm){
                padding-top: 20px;
            }
        }
    }
    label{
        @extend .text--highlight;
        opacity: 1;
        margin-bottom: 0;
        @include media-breakpoint-down(sm){
            font-size: $fontSize__default--mobile;
        }
    }  
    select,
    input[type=email], 
    input[type=number], 
    input[type=password], 
    input[type=search], 
    input[type=tel], 
    input[type=text], 
    input[type=url], 
    select, 
    textarea,
    .quantity{
        border: 1px solid $color__secondary;
        color: $color__secondary;
        letter-spacing: 1px;
        padding: 0 10px;
        &:disabled{
            border-color: $color__grey--light;
            cursor: not-allowed;
        }
    }
    select,
    input[type=email], 
    input[type=number], 
    input[type=password], 
    input[type=search], 
    input[type=tel], 
    input[type=text], 
    input[type=url],
    .quantity{
        height: 40px;
        @include media-breakpoint-down(sm){
            height: 46px;
        }
    }
    .quantity{
        display: flex;
        position: relative;
        padding: 0 26px;
        max-width: 102px;
        label{display: none;}
        input{
            border: 0;
            height: 100%;
            width: 50px;
            text-align: center;
        }
        input[type=number]::-webkit-inner-spin-button, 
        input[type=number]::-webkit-outer-spin-button { 
        -webkit-appearance: none; 
        margin: 0; 
        }
        .quantity-button{
            position: absolute;
            top: 0;
            height: 100%;
            width: 26px;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: $fontSize__medium;
            cursor: pointer;
            &.quantity-up{
                right: 0;
            }
            &.quantity-down{
                left: 0;
            }
        }
    }
    textarea{
        width: 100%;
    }
    select{
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        background-color: $color__white;
        width: 100%;
        &:after{
            content: "sd";
        }
        &:-ms-expand {
            display: none;
        }
        border-radius: 0;
    }
    .form__item{
        margin-bottom: 20px;
        input{
            display: block;
            width: 100%;
        }
        &--checkbox{
            label{
                display: flex;
                align-items: center;
                text-transform: none;
                &:before{
                    content: "";
                    width: 12px;
                    min-width: 12px;
                    height: 12px;
                    display: block;
                    border: 1px solid $color__secondary;
                    margin-right: 13px;
                    border-radius: 1px;
                    transition: all 250ms ease;
                }
            }
            input{
                display: none;
                &:checked + label{
                    &:before{
                        background-color: $color__secondary;
                    }
                }
            }
        }
        &--radio{
            >label{
                display: flex;
                align-items: center;
                text-transform: none;
                position: relative;
                &:before{
                    content: "";
                    width: 14px;
                    height: 14px;
                    min-width: 14px;
                    display: block;
                    border: 1px solid $color__secondary;
                    margin-right: 13px;
                    border-radius: 50%;
                    transition: all 250ms ease;
                }
                &:after{
                    content: "";
                    width: 6px;
                    height: 6px;
                    display: block;
                    background-color: $color__white;
                    border-radius: 50%;
                    position: absolute;
                    top: 50%;
                    left: 4px;
                    z-index: 1;
                    transform: translateY(-50%);
                    transition: all 250ms ease;
                }
            }
            >input{
                display: none;
                &:checked + label{
                    &:before{
                        border-color: $color__primary;
                    }
                    &:after{
                        background-color: $color__primary;
                    }
                }
            }
        }
        &--paiement,
        &--shipment,
        &--carte-cadeau{
            border: 1px solid $color__secondary;
            padding: 15px 25px;
            @include media-breakpoint-down(sm){
                padding: 15px 10px;
            }
            label{
                display: flex;
                span{
                    display: flex;
                    flex-direction: column;
                    &:first-child{
                        flex-grow: 2;
                    }
                }
                span strong,
                .price{
                    @extend .text--highlight;
                    opacity: 1;
                    font-weight: $fontWeight__semi-bold;
                }
                .price{
                    text-align: center;
                    line-height: 1.3rem;
                }
            }
        }
        &--paiement{
            margin-bottom: 0;
            padding: 22px 25px;
            @include media-breakpoint-down(sm){
                padding: 22px 10px;
            }
            label{
                flex-wrap: wrap;
                strong{
                    @include media-breakpoint-down(sm){
                        margin-bottom: 0 !important;
                    }
                }
            }
            .paiement-content{
                display: none;
                margin-top: 50px;
                margin-bottom: 20px;
                width: 100%;
                flex-direction: column;
                @include media-breakpoint-down(sm){
                    margin-top: 20px;
                    label{
                        display: inline !important;
                        span.show--mobile{
                            display: inline !important;
                        }
                    }
                }
            }
            input:checked + label + .paiement-content{
                display: flex;
            }
            
            & + .form__item--paiement{
                margin-top: -1px;
            }
        }
        &--select{
            position: relative;
            select{
                z-index: 2;
                position: relative;
                background-color: transparent;
                padding-right: 30px;
                max-width: 100%;
            }
            i{
                font-size: 0.6rem;
                color: $color__secondary;
                position: absolute;
                bottom: 0;
                right: 1px;
                z-index: 1;
                height: 40px;
                display: flex;
                align-items: center;
                padding-top: 2px;
            }
        }
    }
    .form__footer{
        display: flex;
        justify-content: space-between;
        @include media-breakpoint-down(sm){
            justify-content: space-evenly;
        }
    }
    .form__item--wrapper{
        display: flex;
        justify-content: space-between;
        &:not(.align-item--center){
            >*:last-child{
                flex-grow: 2;
                margin-left: 30px;
            }
        }
    }
    &.form--livraison{
        .billing-address--wrapper{
            display: none;
        }
    }
    &.form--paiement{
        .button{
           margin-top: 35px; 
        }
    }
    &.form--contact{
        .form__item--radio{
            margin-bottom: 30px;
            label{
                text-transform: uppercase;
            }
        }
    }
}