footer{
    background-color: $color__secondary;
    border-top: 4px solid $color__primary;
    padding: 31px 0 19px;
    @include media-breakpoint-down(md){
        .hidden--mobile{
            display: none !important;
        }
        .col-12{
            order: 2;
        }
    }
    .logo{
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
        @include media-breakpoint-down(md){
            margin-bottom: 35px;
        }
        img{
            margin-bottom: 20px;
        }
        span{
            font-weight: $fontWeight__light;
            letter-spacing: 2.8px;
            color: $color__white;
            font-family: $font__secondary;
            text-transform: uppercase;
            text-align: center;
            display: block;
            font-size: $fontSize__default;
            @include media-breakpoint-down(md){
                font-size: 1rem;
            }
        }
    }
    .footer__title{
        color: $color__white;
        font-weight: $fontWeight__bold;
        text-transform: uppercase;
        font-size: $fontSize__default;
        margin-bottom: 10px;
        display: block;
        @include media-breakpoint-down(md){
            @include filter();
            color: $color__white;
            border-top: 1px solid rgba($color__white, .2);
            margin-bottom: 0;
            padding-top: 20px;
            padding-bottom: 20px;
            font-weight: $fontWeight__semi-bold;
            a{
                font-weight: $fontWeight__semi-bold;
            }
        }
        a{
            color: $color__white;
        }
    }
    ul{
        @extend %list--reset;
        @include media-breakpoint-down(md){
            display: none;
            margin-bottom: 30px !important;
            padding-left: 28px !important;
        }
        li{
            padding-bottom: 6px;
            color: $color__white;
            @include media-breakpoint-down(md){
                padding: 10px 0;
            }
            a{
                color: $color__white;
            }
        }
    }
    .footer__autre{
        h4{
            margin-bottom: 20px;
            &:last-child{
                margin-bottom: 8px;
            }
        }
        @include media-breakpoint-down(md){
            .footer__title{
                border-bottom: 1px solid rgba($color__white, .2);
            }
        }
    }
    .footer__rs{
        @include media-breakpoint-down(md){
            order: 1;
        }
        ul{
            @include media-breakpoint-down(md){
                padding-left: 0 !important;
            }
            li{
                @include media-breakpoint-down(md){
                    margin: 0 12px;
                }
                a{
                    display: flex;
                    flex-direction: row;
                    margin: 20px 0;
                    align-items: center;
                    span{
                        margin-left: 10px;
                        @include media-breakpoint-down(md){
                            display: none;
                        }
                    }
                    &:hover{
                        i{
                            @extend .icon:hover;
                        }
                    }
                }
            }
        }
        @include media-breakpoint-down(md){
            text-align: center;
            margin-bottom: 20px;
            ul{
                display: flex;
                flex-direction: row;
                justify-content: center;
            }
        }
    }
    .footer--border{
        padding-bottom: 0px;
        @include media-breakpoint-up(lg){
            border-left: 1px solid rgba($color__primary,.5);
        }
    }
    .footer__mentions{
        margin-top: 43px !important;
        @include media-breakpoint-down(md){
            flex-direction: column;
            margin: 0 !important;
            margin-top: 20px !important;
        }
        li{
            font-size: $fontSize__extra-small;
            font-weight: $fontWeight__light;
            padding: 0;
            @include media-breakpoint-down(md){
                padding: 10px 0;
            }
            a{

            }
            & + li {
                @include media-breakpoint-up(lg){
                    margin-left: 10px;
                    padding-left: 10px;
                    border-left: 1px solid rgba($color__white, .5);
                }
            }
        }
    }
}