$banner__height: 595px;
$banner__height--medium: 400px;
$banner__height--small: 330px;
$banner__height--extra-small: 255px;
$banner__height--mobile: 200px;
%banner-white--mobile{
  @include media-breakpoint-down(sm){
    .banner__wrapper{
      position: relative !important;
      margin-top: -40px;
      .banner__content{
        background-color: $color__white;
        padding: 20px 10px;
        min-height: auto;
        margin-bottom: 0 !important;
      }
    }
  }
}
.banner{
  // background-color: $color__grey--extra-light;
  position: relative;
  overflow: hidden;
  @include media-breakpoint-up(md){
    height: $banner__height;
    // margin: 20px 0 !important;
  }
  &--medium{
    @include media-breakpoint-up(md){
      height: $banner__height--medium;
    }
  }
  &--small{
    @include media-breakpoint-up(md){
      height: $banner__height--small;
    }
  }
  &--extra-small{
    @include media-breakpoint-up(md){
      height: $banner__height--extra-small;
    }
  }
  &--full{
    height: 100%;
    @include media-breakpoint-down(sm){
      height: auto;
    }
  }
  &--contentSize-small{
    .banner__wrapper{
      .banner__content{
        max-width: 230px !important;
        p{
          margin-top: 15px !important;
          margin-bottom: 0;
        }
      }
    }
  }
  &--contentSize-small-light{
    .banner__wrapper{
      .banner__content{
        max-width: 300px !important;
        p{
          margin-top: 0px !important;
          margin-bottom: 0;
        }
      }
    }
  }
  
  &--contentSize-default{
    .banner__wrapper{
      .banner__content{
        max-width: 330px !important;
      }
    }
  }
  &--contentSize-medium{
    .banner__wrapper{
      .banner__content{
        max-width: 450px !important;
      }
    }
  }
  &--contentSize-large{
    .banner__wrapper{
      .banner__content{
        max-width: 520px !important;
      }
    }
  }
  &--left{
    .banner__content-wrapper{
      display: flex;
      justify-content: center;
      @include media-breakpoint-up(md){
        justify-content: flex-start;
        padding-left: 100px;
      }
    }
  }
  &--center{
    .row{
      justify-content: center;
      .banner__content-wrapper{
        display: flex;
        justify-content: center;
      }
    }
  }
  &--right{
    .banner__content-wrapper{
      display: flex;
      justify-content: center;
    }
    @include media-breakpoint-up(md){
      .row{
        justify-content: flex-end;
        .banner__content-wrapper{
          display: flex;
          justify-content: flex-end;
          padding-right: 100px;
        }
      }
    }
  }
  &--Y--center{
    .banner__content-wrapper{
      align-items: center !important;
    }
  }
  &--Y--bottom{
    .banner__content-wrapper{
      align-items: flex-end !important;
      .banner__content{
        margin-bottom: 26px;
      }
    }
  }
  &--textAlign-left{
    .banner__content{
      text-align: left !important;
      align-items: flex-start !important;
    }
  }
  &--textAlign-right{
    .banner__content{
      text-align: right !important;
      align-items: flex-end !important;
    }
  }
  &--imageContainer{
    background-color: transparent;
    .banner__img{
      @extend %responsive--visuel-fullsize;
    }
    &.banner--right{
      .banner__wrapper{
        .banner__content{
          align-items: flex-start;
          text-align: left;
        }
      }
    }
  }
  &--margin,
  &--margin-default{
    margin: 44px 0;
    @include media-breakpoint-down(sm){
      margin: 20px 0;
    }
  }
  &--margin-small{
    margin: 25px 0;
  }
  &--margin-top-none{
    margin: 0 0 44px 0;
  }
  &--margin-none{
    margin: 0;
  }
  &--contentWhite{
    @extend %banner-white--mobile;
    .banner__content{
      @extend .bloc--text-content;
      align-items: center !important;
      min-height: 122px;
      h2{
        text-align: center;
      }
    }
    a.banner__content-wrapper:hover{
      .banner__content{
        background-color: $color__secondary;
        *{
          color: $color__white !important;
        }
        .button{
          color: $color__secondary !important;
          &:before{
            background-color: $color__white;
          }
        }
      }
    }
  }
  &--contentWhiteMobile{
    @extend %banner-white--mobile;
  }
  &--textWhite{
    .banner__content{
      *:not(.button){
        color: $color__white !important;
        @include media-breakpoint-down(sm){
          color: $color__secondary !important;
        }
      }
      .button{
        @include media-breakpoint-down(sm){
          color: $color__secondary !important;
          border-color: $color__secondary !important;
        }
      }
    }
    a.banner__content-wrapper:hover{
      .button{
        color: $color__secondary !important;
      }
    }
  }
  &--topOfPage{
    &.banner--right {
      text-align: left !important;
      align-items: flex-start !important;
    }
    @include media-breakpoint-down(sm){
      height: 64px;
      margin-bottom: 0;
      .banner__img{
        padding-bottom: 64px !important;
      }
      .banner__wrapper{
        .banner__content{
          > * {
            display: none;
          }
          h2{
            display: block;
          }
        }
      }
    }
  }
  &--box{
    .banner__wrapper{
      .container{
        .banner__content-wrapper{
          .banner__content{
            padding: 30px;
            align-items: flex-start !important;
            @include media-breakpoint-down(sm){
              padding: 20px;
            }
            p{
              margin-top: 5px !important;
              @include media-breakpoint-down(sm){
                margin: 0 auto;
              }
            }
            @include media-breakpoint-down(sm){
              h2{
                margin-bottom: 5px !important;
                margin: 0 auto;
              }
              .text--category{
                display: block;
                margin: 0 auto 5px;
              }
            }
            .banner__content-footer {
              align-items: flex-start;
              flex-direction: inherit;
              @include media-breakpoint-down(sm){
                // margin-top: 5px;
                .price{
                  // padding-bottom: 5px;
                  display: block;
                  margin: 0 auto;
                }
                .form__footer{
                  width: 100%;
                  flex-direction: column;
                  align-items: center;
                  .button{
                    // margin: 0 auto;
                    // margin-top: 10px;
                    input{
                      // padding: 10px;
                    }
                  }
                }
                .quantity{
                  // width: 82px;
                  // input{
                  //   width: 30px;
                  //   padding: 0;
                  // }
                }
              }
              .form__footer{
                @include media-breakpoint-down(sm){
                  flex-direction: row;
                  margin: 0 -15px;
                  width: calc( 100% + 30px );
                }
                > * + * {
                  margin-left: 20px;
                  @include media-breakpoint-down(sm){
                    margin-left: 0;
                  }
                }
              }
            }
            ul{
              text-align: left;
              margin-top: 20px;
              @include media-breakpoint-down(sm){
                // display: none;
              }
              li{
                text-transform: uppercase;
                line-height: $fontSize__default + 2rem;
                letter-spacing: 0.93px;
                @include media-breakpoint-down(sm){
                  text-align: center;
                  font-size: $fontSize__default--mobile;
                  line-height: $fontSize__default--mobile + 1rem;
                  & + li{
                    margin-top: 15px;
                  }
                }
                &:before{
                  content: "-";
                  padding-right: 5px;
                }
              }
            }
          }
        }
      }
    }
    &:hover{
      .banner__wrapper:hover{
        .container{
          .banner__content-wrapper{
            .banner__content{     
              .button{
                &:before{
                  width: 0;
                  height: 0;
                }
                &:hover{
                  &:before{
                    width: 200%;
                    height: 300%;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  .banner__img-wrapper{height: 100%;}
  .banner__img{
    background-position: center;
    padding-bottom: 71.875%;
    @include media-breakpoint-up(md){
      padding-bottom: 0;
      height: 100%;
      // height: $banner__height--mobile;
    }
    img{
      width: 100%;
    }
  }
  .banner__wrapper{
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    // @include media-breakpoint-down(sm){
    //   position: absolute;
    //   top: auto;
    //   bottom: 20px;
    //   margin-top: 0px;
    //   height: auto;
    // }
    &--hidden{
      display: none;
    }
    .container{
      height: 100%;
      .row, .col-12{
        height: 100%;
      }
      .banner__content-wrapper{
        height: 100%;
        display: flex;
        align-items: center;
        @include media-breakpoint-down(sm){
          padding: 0;
        }
        .banner__content{
          flex-direction: column;
          display: flex;
          text-align: center;
          align-items: center;
          width: 100%;
          max-width: 330px;
          transition: background 250ms ease;
          @include media-breakpoint-down(sm){
            align-items: center !important;
          }
          // @include media-breakpoint-down(sm){
          //   background-color: $color__white;
          //   padding: 10px;
          //   margin-bottom: 0;
          // }
          >*{
            max-width: 100%;
          }
          *{
            transition: background 250ms ease;
          }
          .text--category{
            @include media-breakpoint-down(sm){
              font-size: $fontSize__extra-extra-small;
            }
          }
          h2{
            margin-bottom: 0px;
            color: $color__secondary;
          }
          p{
            margin-top: 15px;
            margin-bottom: 0;
            color: $color__secondary;
          }
          ul{
            @extend %list--reset;
            li{
              color: $color__secondary;
            }
          }
          &-footer{
            display: flex;
            width: 100%;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            margin-top: 20px;
            .price{
              // color: $color__secondary;
              // font-size: $fontSize__big;
              // letter-spacing: 1.72px;
              font-family: $font__secondary;
              font-size: $fontSize__medium;
              line-height: $fontSize__medium + 0.3rem;
              font-weight: $fontWeight__semi-bold;
              display: block;
              color: $color__secondary;
              padding-bottom: 20px;
            }
            .button{
              display: inline-block;
              @include media-breakpoint-down(sm){
                max-width: fit-content;
              }
            }
            &--row{
              flex-direction: row;
              @include media-breakpoint-down(sm){
                flex-direction: column;
              }
              .price{
                font-family: $font__secondary;
                font-size: $fontSize__medium;
                color: $color__secondary;
                font-weight: $fontWeight__bold;
                letter-spacing: 0px;
                padding: 0;
                @include media-breakpoint-down(sm){
                  padding-bottom: 20px;
                }
                & + *{
                  margin-left: 20px;
                  @include media-breakpoint-down(sm){
                    margin-left: 0;
                  }
                }
              }
            }
          }
        }
        &:hover{
          .button{
            @extend .button:hover;
          }
        }
      }
      // a[data-youtube-id]{
      //   @include media-breakpoint-down(sm){
      //     .banner__content{
      //       background-color: transparent;
      //       > * {
      //         display: none;
      //       }
      //       .banner__content-footer {
      //         display: block;
      //         margin-top: 0;
      //         .button{
      //           background-color: $color__white;
      //         }
      //       }
      //     }
      //   }
      // }
    }
  }
  & + .banner{
    margin-top: 30px;
    @include media-breakpoint-down(sm){
      margin-top: 20px;
    }
  }
  
}
.banners{
  @include media-breakpoint-down(sm){
    .col-12 + .col-12 .banner{
      margin-top: 20px !important;
    }
  }
  .banner{
    @extend %responsive--visuel-fullsize;
  }
}