$header__icon-height: 20px;

header{
  background-color: $color__white;
  z-index: 20;
  position: fixed;
  height: $header__height-mobile;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  transition: all 500ms ease;
  @include media-breakpoint-up(lg){
    height: $header__height-tablet;
    position: absolute;
  }
  @include media-breakpoint-up(xl){
    height: $header__height-default;
  }
  .logo{
    display: block;
    text-align: center;
    // max-width: 100px;
    margin: 0 auto;
    @include media-breakpoint-down(md){
      padding: 7px;
    }
    @include media-breakpoint-down(md){
      height: $header__height-mobile;
    }
    @include media-breakpoint-up(lg){
      max-width: initial;
    }
    img{
      transition: all 500ms ease;
      @include media-breakpoint-down(md){
        max-height: 100%;
      }
      @include media-breakpoint-up(lg){
        // max-width: 192px;
      }
      @include media-breakpoint-up(xl){
        max-width: 100%;
      }
    }
  }
  .header__pro{
    border-bottom: 1px solid $color__grey--light ;
    padding: 2px 0;
    @include media-breakpoint-down(md){
      display: none;
    }
    ul{
      @extend .list--horizontal;
      justify-content: flex-end;
      li{
        padding-left: 30px;
        a{
          @extend .text--highlight;
          font-size: $fontSize__extra-extra-small;
          font-weight: $fontWeight__bold;
          &:hover{
            opacity: 1;
          }
        }
      }
    }
  }
  .header__top{
    padding: 0;
    transition: all 500ms ease;
    height: $header__height-mobile;
    display: flex;
    align-items: center;
    box-sizing: content-box;
    @include media-breakpoint-down(sm){
      .col-3:last-child{
        padding-left: 0;
      }
    }
    @include media-breakpoint-up(lg){
      display: block;
      padding: 15px 0 13px;
      height: auto;
      height: initial;
    }
    @include media-breakpoint-up(xl){
      padding: 25px 0 40px;
    }
    .navbar-tools{
      display: flex;
      justify-content: flex-end;
      transition: $transition-base;
      @include media-breakpoint-down(md){
        height: 100%;
      }
      @include media-breakpoint-up(lg){
        padding-top: 12px;
      }
      @include media-breakpoint-up(xl){
        padding-top: 25px;
      }
      >ul{
        align-items: center;
        @include media-breakpoint-down(md){
          align-items: center;
          width: 100%;
          justify-content: space-around;
        }
        >li{
          display: block;
          height: $header__icon-height;
          position: relative;
          &.e-shop--access{
            height: auto;
            z-index: 2000;
            @include media-breakpoint-down(md){
              z-index: 0;
            }
            .inner{
              background-color: $color__white;
              address{
                font-size: $fontSize__default;
                line-height: $fontSize__default + 1.1rem;
              }
              & > a{
                display: flex;
                align-items: center;
                font-weight: $fontWeight__bold;
                color: $color__secondary;
                text-transform: uppercase;
                letter-spacing: 1px;
                font-size: $fontSize__extra-small;
                text-align: center;
                line-height: 100%;
                span{
                  @include media-breakpoint-down(md){
                    display: none;
                  }
                }
                i.icon-fleche{
                  font-size: .8rem;
                  color: $color__secondary;
                  font-weight: $fontWeight__bold;
                  @include media-breakpoint-down(md){
                    display: none;
                  }
                }
                i.icon-store{
                  height: $header__icon-height;
                  display: flex;
                  align-items: center;
                  color: $color__text;
                  cursor: pointer;
                  font-size: $fontSize__medium;
                }
              }
              .info{
                height: 0;
                transition: all 1s ease;
                overflow: hidden;
                display: none;
                address{
                  margin: 20px 0;
                  p{
                    margin-bottom: 1rem;
                  }
                }
                .button + .button{
                  margin-top: 12px;
                }
              }
            }
          }
          >a{
            font-size: 1.8rem;
            height: $header__icon-height;
            color: $color__secondary;
            margin-left: 20px;
            display: block;
            cursor: pointer;
            @include media-breakpoint-down(md){
              margin-left: 0;
            }
            i{
              height: $header__icon-height;
              display: flex;
              align-items: center;
              color: $color__text;
              cursor: pointer;
              font-size: $fontSize__medium;
            }
            &:hover i{
              color: $color__primary;
            }
            &.cart__access{
              position: relative;
              & > span{
                position: absolute;
                right: -2px;
                bottom: -5px;
                background-color: $color__primary;
                border-radius: 50%;
                width: 16px;
                height: 16px;
                font-size: $fontSize__breadcrumb;
                display: flex;
                justify-content: center;
                align-items: center;
                color: $color__white;
              }
            }
          }
          &:first-child a{
            margin-left: 0;
          }
        }
        &.e-shop--enabled{
          width: 100%;
          .e-shop--access{
            flex-grow: 2;
            position: relative;
            min-height: 30px;
            @include media-breakpoint-down(md){
              flex-grow: 0;
              min-height: auto;
            }
            .inner{
              & > a{
                justify-content: center;
              }
              .info{
                display: block;
                @include media-breakpoint-down(md){
                  display: none;
                }
              }
            }
            &:hover{
              @include media-breakpoint-down(md){
                z-index: 2000;
              }
              .inner{
                position: absolute;
                top: -24px;
                left: 50%;
                transform: translateX(-50%);
                box-shadow: 0 5px 11px -6px rgba(0,0,0,.38);
                width: 266px;
                padding: 24px 26px;
                @include media-breakpoint-down(xs){
                  transform: translateX(-70%);
                }
                span{
                  @include media-breakpoint-down(md){
                    display: block;
                  }
                }
                .info{
                  height: auto;             
                  @include media-breakpoint-down(md){
                    display: block;
                  }
                }
              }
            }
          }
        }
      }
    }
    .navbar-sur-menu{
      transition: $transition-base;
      @include media-breakpoint-up(lg){
        padding-top: 10px;
      }
      @include media-breakpoint-up(xl){
        padding-top: 24px;
      }
      @include media-breakpoint-down(md){
        padding-top: 0;
        align-items: center;
        display: flex;
        height: 100%;
      }
      .navbar__toggler{
        background-color: transparent;
        border: 0;
        padding: 0 8px;
        i{
          color: $color__secondary;
        }
        @include media-breakpoint-down(md){
          padding: 0;
        }
      }
      ul{
        li{
          &.lang{
            position: relative;
            a{
              z-index: 2;
              position: relative;
              i{
                width: auto;
              }
            }
            ul{
              background-color: $color__white;
              display: block;
              position: absolute;
              top: 0;
              left: -5px;
              @extend %list--reset;
              padding: 26px 5px 0 !important;
              box-shadow: 0 0px 10px 0 rgba(0,0,0,.2);
              transform: translateY(10px);
              transition: all 0ms ease;
              height: 0;
              overflow: hidden;
              opacity: 0;
              li{
                border-top: 1px solid $color__grey;
                a{
                  color: $color__grey;
                  display: block;
                  width: 100%;
                  &:hover{
                    color: $color__secondary;
                  }
                }
              }
            }
            &:hover{
              > ul{
                display: block;
                transform: translateY(0px) !important;
                z-index: 1;
                height: auto;
                opacity: 1;
                transition-duration: 250ms;
              }
            }
          }
          a{
            font-size: $fontSize__extra-small;
            text-transform: uppercase;
            color: $color__secondary;
            letter-spacing: 1px;
            margin-right: 30px;
            font-size: $fontSize__extra-small;
            display: flex;
            i{
              font-size: 6px;
              display: inline-block;
              margin-left: 5px;
              color: $color__text;
            }
            &.marque{
              font-weight: $fontWeight__bold;
              position: relative;
              @include dot;
            }
            &:hover{
              color: $color__primary;
            }
          }
          &:last-child a{
            margin-right: 0;
          }
        }
      }
    }
  }
  .header__nav{
    background-color: $color__white;
    position: relative;
    width: calc(100% - 30px);
    margin-top: -$header__height-mobile;
    box-shadow: 0 0px 20px 10px rgba(0,0,0,0.3);
    transition: transform 500ms ease;
    transform: translateX(-100%);
    min-height: calc(100vh - 30px);;
    @include media-breakpoint-up(lg){
      background-color: $color__secondary;
      width: 100%;
      min-height: auto;
      margin-top: 0;
      box-shadow: none;
      transform: translateX(0);
    }
    .navbar__header{
      height: $header__height-mobile;
      display: flex;
      align-items: center;
      justify-content: space-between;
      .navbar__toggler{
        @extend .button--close;
      }
      .lang{
        @extend .list--horizontal;
        padding: 0 10px !important;
        li{
          & + li{
            border-left: 1px solid $color__secondary;
          }
          a{
            padding: 0 10px;
            color: $color__secondary;
            display: block;
            line-height: 100%;
            &.active{
              font-weight: $fontWeight__bold;
            }
          }
        }
      }
    }
    >.container{
      @include media-breakpoint-down(md){
        max-width: 100%;
      }
      >.row{
        >.col-12{
          position: inherit;
          padding: 0;
        }
      }
    }
    nav.navbar .navbar__wrapper .navbar__collapse .navbar__menu{
      padding: 0 20px;
      @include media-breakpoint-up(lg){
        padding: 0;
      }
      
    }
  }
}

body{
  padding-top: $header__height-default;
  transition: padding 500ms ease;
  padding-top: $header__height-mobile !important;
  @include media-breakpoint-up(lg){
    padding-top: $header__height-tablet !important;
  }
  @include media-breakpoint-up(xl){
    padding-top: $header__height-default !important;
  }
  &.nav-fixed{
    padding-top: $header__height-mobile !important;
    @include media-breakpoint-up(lg){
      padding-top: $header__height-fixed + 4px !important;
    }
    header{
      position: fixed;
      .header__pro{
        display: none;
      }
      @include media-breakpoint-up(lg){
        height: $header__height-fixed;
      }
      .header__top{
        @include media-breakpoint-up(lg){
          padding: 15px 0 13px;
          .navbar-sur-menu{
            padding-top: 10px;
          }
          .navbar-tools{
            padding-top: 12px;
          }
        }
      }
      .logo{
        img{
          @include media-breakpoint-up(lg){
            max-width: 192px;
          }
          // @include media-breakpoint-down(sm){
          //   max-width: 100%;
          // }
        }
      }
    }
  }
}

body.navbar--opened{
  .header__nav{
    @include media-breakpoint-down(md){
      transform: translateX(0);
    }
  }
}