/**************************************
** H1 **
**************************************/
@include hStyle('h1'){
  margin-top: 0;
  font-family: $font__secondary;
  font-size: $fontSize__title-H1;
  line-height: $fontSize__title-H1 + 1rem;
  margin-bottom: 20px;
  @include media-breakpoint-down(md){
    font-size: $fontSize__title-H1--mobile;
    line-height: $fontSize__title-H1--mobile + 0.5rem;
  }
}
/**************************************
** H2 **
**************************************/
@include hStyle('h2'){
  font-family: $font__secondary;
  font-size: $fontSize__title-H2;
  line-height: $fontSize__title-H2 + 1rem;
  @include media-breakpoint-down(md){
    font-size: $fontSize__title-H2--mobile;
    line-height: $fontSize__title-H2--mobile + 0.5rem;
  }
}
/**************************************
** H3 **
**************************************/
@include hStyle('h3'){
  // font-family: $font__secondary;
  // font-size: $fontSize__title-H3;
  // line-height: $fontSize__title-H3 + 1rem;
  @extend .text--highlight;
  font-size: $fontSize__default;
  opacity: 1;
  @include media-breakpoint-down(md){
    font-size: $fontSize__title-H3--mobile;
    line-height: $fontSize__title-H3--mobile + 0.2rem;
    // margin-bottom: 20px;
  }
}
