a{
  color: $color__main;
  transition: $transition-base;
  &:hover{
    color: $color__primary;
    text-decoration: none;
  }
}
p{
  margin-bottom: 2rem;
  big{
    font-size: $fontSize__default;
    line-height: $fontSize__default + 1.1rem;
    letter-spacing: 0.93px;
    font-weight: $fontWeight__bold;
  }
}

blockquote{
  .icon-guillemet{
    font-size: 20px;
    margin-bottom: 30px;
    display: inline-block;
  }
  // @extend h3;
  font-family: $font__secondary;
  font-size: $fontSize__big;
  line-height: $fontSize__big + .4rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-align: left;
  align-items: stretch;
  color: $color__secondary;
  @include media-breakpoint-down(sm){
    font-size: $fontSize__medium;
    line-height: $fontSize__medium + .6rem;
  }
}