
html {
  -moz-osx-font-smoothing: antialiased;
  -webkit-font-smoothing: antialiased;
  font-size: 62.5%;
  @include media-breakpoint-down(sm){
    overflow-x: hidden;
  }
}
body{
  font-size: $fontSize__default;
  line-height: $fontSize__default + 1.1rem;
  color: $color__secondary;
  font-family: $font__main;
  scroll-behavior: smooth;
  @include media-breakpoint-down(sm){
    font-size: $fontSize__default--mobile;
    line-height: $fontSize__default--mobile + 1rem;
    overflow-x: hidden;
  }
}
#isMobile{
  display: none;
  @include media-breakpoint-down(sm){
    display: block;
  }
}
#content{
  position: relative;
}
.blur-up {
  -webkit-filter: blur(5px);
  filter: blur(5px);
  transition: filter 400ms, -webkit-filter 400ms;
}

.blur-up.lazyloaded {
  -webkit-filter: blur(0);
  filter: blur(0);
}

[aria-label][role~="tooltip"]::after{
  background-color: $color__secondary;
}
[role~="tooltip"][data-microtip-position|="bottom"]::before {
  background: url("data:image/svg+xml;charset=utf-8,%3Csvg%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20width%3D%2236px%22%20height%3D%2212px%22%3E%3Cpath%20fill%3D%22rgba%2857,%2065,%2071,%201%29%22%20transform%3D%22rotate%28180%2018%206%29%22%20d%3D%22M2.658,0.000%20C-13.615,0.000%2050.938,0.000%2034.662,0.000%20C28.662,0.000%2023.035,12.002%2018.660,12.002%20C14.285,12.002%208.594,0.000%202.658,0.000%20Z%22/%3E%3C/svg%3E") no-repeat;
}