.video{
  position: relative;
  display: block;
  &__source{
    video{
      width: 100%;
      display: block;
    }
  }
  &__wrapper{
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    display: flex;
    align-items: center;
    @include media-breakpoint-down(sm){
      position: relative;
      margin-top: -20px;
    }
    .video__bg{
      position: absolute;
      width: 100%;
      transition: $transition-base;
      img{
        width: 100%;
      }
    }
    *{
      color: $color__secondary;
      transition: $transition-base;
    }
    .video__content{
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      @include media-breakpoint-down(sm){
        background-color: $color__white;
        padding: 10px;
      }
      >*{
        max-width: 100%;
      }
      .text--category{
      }
      .video__title{
      }
      p{
        margin-bottom: 36px;
      }
    }
  }
  &.play{
    .video__bg{
      opacity: 0;
    }
    .video__content{
      *{
        opacity: 0;
      }
      .button{
        opacity: 1;
        @extend .button--white;
      }
    }
  }
}
.player-video{
  span{display: none;}
  width: 76px;
  height: 76px;
  background-color: $color__primary;
  position: relative;
  transition: all 250ms ease;
  box-shadow: 0 0 6px rgba(0,0,0,.65);
  border-radius: 38px;
  display: block;
  span{display: none;}
  &:before{
    content: " ";
    width: 90px;
    height: 90px;
    border: 1px solid $color__white;
    border-radius: 45px;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -45px;
    margin-top: -45px;
    transition: all 250ms ease;
  }
  &:after{
    content: " ";
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 16px 0 16px 26px;
    border-color: transparent transparent transparent #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -16px;
    margin-left: -10px;
    transition: all 250ms ease;
  }
  &.hover,
  &:hover{
    transform: scale(1.1);
    box-shadow: 0 0 12px rgba(0,0,0,.85);
    &:before{
      border-radius: 70px;
      width: 140px;
      height: 140px;
      margin-left: -70px;
      margin-top: -70px;
      opacity: 0;
    }
  }
}

video{
  max-width: 100%;
}