.text--uppercase { text-transform: uppercase; }

.text-weight--light{ font-weight: $fontWeight__light !important;}
.text-weight--bold{ font-weight: $fontWeight__bold !important;}

.text-align--left{text-align: left;}
.text-align--center{text-align: center;}
.text-align--right{text-align: right;}


.text-color--main{color: $color__main;}
.text-color--primary{color: $color__primary;}
.text-color--secondary{color: $color__secondary;}
.text-color--tertiary{color: $color__primary;}

.text--big{
  font-size: $fontSize__small-plus;
  line-height: $fontSize__small-plus + 2rem;
  letter-spacing: 1.45px;
  @include media-breakpoint-down(sm){
    font-size: $fontSize__default;
    line-height: $fontSize__default + 1.8rem;
  }
}
.text--highlight,
.text--category{
    text-transform: uppercase;
    letter-spacing: 0.93px;
    // opacity: 0.5;
    opacity: 0.7;
    // color: $color__black;
    color: $color__secondary;
    display: inline-block;
    font-size: $fontSize__extra-small;
    font-family: $font__main;
    @include media-breakpoint-down(sm){
      font-size: 1rem;
      line-height: $fontSize__extra-small;
      margin-bottom: 5px;
    }
}
.text--legend{
  color: $color__grey;
  font-size: $fontSize__extra-extra-small;
  line-height: $fontSize__extra-extra-small + .6rem;
}

.text--intro{
  font-weight: $fontWeight__semi-bold;
  font-family: $font__secondary;
  font-size: $fontSize__small-plus;
}
.text--temoignage{
  font-family: $font__secondary;
  font-style: italic;
  font-size: $fontSize__big-plus;
  text-align: center;
  letter-spacing: 1.86px;
  line-height: $fontSize__big-plus + 1.9rem;
  color: $color__primary;
  margin-bottom: 0;
  font-weight: $fontWeight__semi-bold;
  @include media-breakpoint-down(sm){
    font-size: $fontSize__medium-plus;
    line-height: $fontSize__medium-plus + .8rem;
    padding: 30px 0;
  }
}
.text-hidden{
  position: absolute;
  left: -10000em;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}



.link{
  display: block;
  letter-spacing: 0.93px;
  color: $color__primary;
  &:before{
    content: ">";
    padding-right: 5px;
  }
  &:hover{
    color: $color__secondary;
  }
}
.link--back{
  @extend .text--highlight;
  display: flex;
  align-items: center;
  color: $color__secondary;
  opacity: 1;
  .icon{
      color: $color__secondary;
      font-size: $fontSize__breadcrumb;
  }
  &--margin{
    margin-bottom: 30px;
  }
}
.link--underline{
  color: $color__secondary;
  text-decoration: underline;
}
.link--checkbox{
  display: flex;
  align-items: center;
  text-transform: none;
  @extend .text--highlight;
  font-weight: $fontWeight__default;
  color: $color__secondary;
  text-transform: uppercase;
  opacity: 1;
  padding-left: 10px;
  padding-bottom: 14px;
  &:after{
    content: "";
    width: 12px;
    min-width: 12px;
    height: 12px;
    display: block;
    border: 1px solid $color__secondary;
    margin-left: 13px;
    border-radius: 1px;
    transition: all 250ms ease;
  }
  &.checked{
    &:after{
        background-color: $color__secondary;
    }
  }
}

.title--margin{
  margin-bottom: 62px;
  margin-top: 11px;
  @include media-breakpoint-down(sm){
    margin-top: 0;
    margin-bottom: 32px;
  }
}
.title--routine{
  color: $color__text;
  font-size: $fontSize__big;
  letter-spacing: 1.72px;
  font-weight: $fontWeight__semi-bold;
  text-transform: uppercase;
  small{
    display: block;
    font-size: $fontSize__medium;
    letter-spacing: 1.29px;
    font-weight: $fontWeight__semi-bold;
  }
}
.title--highlight{
  @extend .text--highlight;
  opacity: 1;
  margin-bottom: 0;
  font-size: $fontSize__default;
  line-height: $fontSize__default + .9rem;
}
.title--big{
    text-transform: uppercase;
    margin-bottom: 30px;
    font-size: $fontSize__title-H3;
    font-weight: $fontWeight__semi-bold;
    line-height: $fontSize__title-H3 + 0.8rem;
    &--noMarge{
      margin-bottom: 0;
    }
}
.title--mag{
  text-align: center;
  margin-bottom: 30px;
  .icon{
    font-size: 82px;
    width: 82px;
    height: 82px;
    margin: 0 auto;
    color: $color__secondary;
    margin-bottom: -33px;
    padding-left: 35px;
    @include media-breakpoint-down(sm){
      margin-bottom: -26px;
      padding-left: 25px;
    }
  }
  span{
    font-size: 6rem;
    @include media-breakpoint-down(sm){
      font-size: 4.8rem;
    }
    & > span{
      color: $color__primary;
      font-style: italic;
    }
  }
}
.title--h2-medium,
.title--h2-small{
  @extend .h2;
  text-transform: none;
  font-size: $fontSize__big !important;
  font-weight: $fontWeight__default !important;
  display: block;
}
.title--h2-small{
  line-height: 3rem !important;
}
.title--h2-medium{
  font-size: $fontSize__large !important;
}
.title--sep{
  font-size: $fontSize__medium-plus;
  line-height: $fontSize__medium-plus + 1.2rem;
  font-family: $font__secondary;
  letter-spacing: 0.92px;
  font-weight: $fontWeight__semi-bold;
  position: relative;
  padding-bottom: 22px;
  margin-bottom: 50px;
  color: $color__secondary;
  @include media-breakpoint-down(sm){
    margin-top: 20px !important;
    padding-bottom: 18px;
    margin-bottom: 36px !important;
    font-size: $fontSize__small-plus;
    line-height: $fontSize__small-plus + 1.2rem;
  }
  &:after{
    content: "";
    display: block;
    width: 165px;
    height: 1px;
    background-color: $color__primary;
    position: absolute;
    bottom: 0;
    left: 0;
  }
  &--center{
    text-align: center;
    &:after{
      left: 50%;
      transform: translateX(-50%);
    }
  }
}
.title--border{
  display: block;
  width: 100%;
  text-align: center;
  @include media-breakpoint-up(md){
    overflow: hidden;
  }
  @include media-breakpoint-up(lg){
  }
  small{
    color: $color__primary;
    font-weight: $fontWeight__bold;
  }
  > *:not(span){
    display: inline-block;
    position: relative;
    padding: 12px 20px;
    margin: 0 20px;
    @include media-breakpoint-up(lg){
      margin: 0;
      padding: 12px 55px;
      max-width: 730px;
    }
    @include media-breakpoint-down(md){
      padding: 12px 10px;
    }
    &:before,
    &:after{
      content: "";
      height: 1px;
      width: 50vw;
      display: block;
      background-color: $color__secondary;
      position: absolute;
      top: 50%;
      @include media-breakpoint-down(sm){
        // display: none;
      }
    }
    &:before{
      right: 100%;
    }
    &:after{
      left: 100%;
    }
  }
  span{
    display: block;
    text-align: center;
    opacity: 0.6;
    font-weight: $fontWeight__light;
  }
  &--width-auto{
    @include media-breakpoint-up(lg){
      > *:not(span){
        padding: 12px 55px;
        width: auto;
      }
    }
  }
  &--small{
    > *{
      @include media-breakpoint-up(lg){
        padding: 0 35px !important;
      }
    }
  }
  &--extra-small{
    > *{
      @include media-breakpoint-up(lg){
        padding: 0 15px !important;
      }
    }
  }
  &--margin-small{
    margin-bottom: 20px;
    @include media-breakpoint-up(lg){
      margin-bottom: 30px;
    }
  }
  &--margin{
    margin-bottom: 20px;
    @include media-breakpoint-up(lg){
      margin-bottom: 76px;
    }
  }
  &--left{
    text-align: left;
    &:before{display: none;}
    > *{
      padding-left: 0 !important;
    }
  }
  &--top{
    transform: translateY(-50%);
  }
  &--none{
    h2{
      &:before,&:after{
        display: none;
      }
    }
  }
}
.title--border__surTitle{
  margin: 0 auto;
  display: block;
  @extend .text--highlight;
  opacity: 1;
  text-align: center;
  margin-bottom: -15px;  
}
.title--border__subTitle{
  margin-top: -10px;
  @include media-breakpoint-down(sm){
    margin-top: 0;
  }
}
p a:not(.button){
  color: $color__primary;
  text-decoration: underline;
}