.breadcrumb{
    padding: 20px 0 8px;
    margin: 0;
    // background-color: $color__white;
    background-color: transparent;
    .breadcrumb-item, li.active, li a{
        text-transform: uppercase;
        color: $color__grey;
        letter-spacing: 0.84px;
        font-size: $fontSize__breadcrumb;
        &:before{
            color: $color__grey;
        }
        a:hover{
            color: $color__secondary;
        }
    }
}