#scrollToTopControl{
  position: fixed;
  cursor: pointer;
  transition: all 250ms ease;
  bottom: 20px;
  right: 20px;
  width: 44px;
  height: 44px;
  background-color: rgba($color__primary, 0.9);
  z-index: 2000;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  border-radius: 50%;
  @include media-breakpoint-down(md){
    width: 34px;
    height: 34px;
  }
  .scrollToTop__icon{
    color: $color__white;
    font-size: $fontSize__default;
    transform: rotate(-180deg);
    transition: transform 250ms ease;
    display: flex;
    margin-top: -3px;
  }
  &.active{
    opacity: 1;
  }
  &:hover{
    background-color: rgba($color__primary, 1);
    .scrollToTop__icon{
      @include media-breakpoint-up(lg){
        transform: translateY(-4px) rotate(-180deg);
      }
    }   
  }
}