$card__marging : 10px;
%card--border{
  position: relative;
  &:after{
    content: "";
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 4px;
    background-color: $color__primary;
  }
}
%card--reverse-row{
  @include media-breakpoint-down(sm){
    flex-direction: row-reverse;
  }
}
.card{
  margin: $card__marging 0;
  display: flex;
  flex-direction: column;
  padding-bottom: 40px;
  .card__visuel{
    overflow: hidden;
    position: relative;
    width: 100%;
    i.icon{
      font-size: 88px;
      height: 88px;
      width: 100%;
      display: flex;
      max-width: fit-content;
      align-items: center;
    }
    img,picture{
      width: 100%;
      height: auto;
      transition: all 1000ms ease;
      &.card__visuel--overlay{
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        transform: scale(1.2);
        opacity: 0;
      }
    }
  }
  .card__content__wrapper{
    z-index: 1;
  }
  .card__content{
    padding: 13px 20px 23px;
    text-align: center;
    align-items: center;
    display: flex;
    flex-direction: column;
    position: relative;
    @include media-breakpoint-down(sm){
      padding: 10px 0;
    }
    >*{
      max-width: 100%;
    }
    .tag{
      position: absolute;
      bottom: 100%;
    }
    p{
      color: $color__secondary;
      max-width: 100%;
    }
    .price,
    .card__title{
      font-family: $font__secondary;
      font-size: $fontSize__medium;
      line-height: $fontSize__medium + 0.3rem;
      font-weight: $fontWeight__semi-bold;
      display: block;
      color: $color__secondary;
      @include media-breakpoint-down(sm){
        font-size: $fontSize__small;
        line-height: $fontSize__small + .3rem;
      }
    }
    .price{
      margin-top: 15px;
    }
    .button{
      margin-top: 7px;
    }
  }
  &:hover{
    .card__visuel{
      img,picture{
        transform: scale(1.1);
        &.card__visuel--overlay{
          transform: scale(1);
          opacity: 1;
        }
      }
    }
    .button{
      @extend .button:hover;
    }
  }
  &--largeMobile{
    @extend %responsive--visuel-fullsize;
    .card__content__wrapper{
      margin-top: -40px !important;
      padding: 0 20px !important;
      .card__content{
        max-width: 100% !important;
        padding: 20px;
        .text--category{
          font-size: $fontSize__extra-extra-small;
          margin-bottom: 10px;
        }
        .card__title, .price{
          font-size: $fontSize__medium;
          line-height: $fontSize__medium + .3rem;
        }
      }
    }
  }
  &--simple{
    margin: 0;
    .card__content{
      padding-left: 0;
      padding-right: 0;
      text-align: left;
    }
  }
  &--light{
    padding-bottom: 0;
    .card__content{
      padding-bottom: 0;
    }
  }
  &--menu,
  &--category{
    padding-bottom: 0;
    @extend %card--border;
    .card__content__wrapper{
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
    }
    .card__content{
      // margin: 0 60px;
      max-width: 230px;
      margin: 0 auto;
      margin-bottom: 40px;
      padding-left: 10px;
      padding-right: 10px;
      @extend .bloc--text-content;
      @include media-breakpoint-down(lg){
        // margin: 0 40px;
        margin-bottom: 20px;
      }
      .card__title{
        font-size: $fontSize__big-minus;
        line-height: $fontSize__big-minus + 0.3rem;
        text-transform: uppercase;
        color: $color__secondary;
        opacity: 1;
        letter-spacing: 0.92px;
        @include media-breakpoint-down(sm){
          font-size: $fontSize__medium;
          line-height: $fontSize__medium + 0.3rem;
        }
      }
      .text--category{
        // margin-bottom: 6px;
      }
    }
    
  }
  &--category{
    margin: 15px 0;
    @include media-breakpoint-down(sm){
      flex-direction: row;
      margin: 0;
      &:after{
        display: none;
      }
    }
    .card__visuel{
      @include media-breakpoint-down(sm){
        width: 50%;
        border-bottom: 2px solid $color__primary;
      }
    }
    .card__content__wrapper{
      @include media-breakpoint-down(md){
        position: relative;
        width: 100%;
        .card__content{
          margin: 0 !important;
          width: 100%;
          max-width: 100%;
        }
      }
      @include media-breakpoint-down(sm){
        width: 50%;
        position: relative;
        margin: 0 !important;
        display: flex;
        align-items: center;
        justify-content: center;
        .card__content{
          margin: 0 !important;
          max-width: 100%;
          padding: 10px;
          .button{
            display: none;
          }
          &:after{
            content: "";
            font-size: $fontSize__big;
            color: $color__primary;
            margin-top: -5px;
          }
        }
      }
    }
    &.card--no-border{
      .card__content{
        margin-bottom: 0;
      }
    }
  }
  &--formule,
  &--cadeau,
  &--soin,
  &--spa{
    overflow: hidden;
    margin: 15px 0;
    position: relative;
    @include media-breakpoint-down(md){
      padding-bottom: 0;
    }
    .card__visuel{
      transition: all 250ms ease;
      &:before{
        transition: all 250ms ease;
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        display: block;
        z-index: 1;
        background-color: $color__secondary;
        opacity: 0;
        mix-blend-mode: multiply;
      }
    }
    .card__content__wrapper{
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;

      @include media-breakpoint-down(md){
        position: relative;
      }
    }
    .card__content{
      max-width: 230px;
      margin: 0 auto;
      margin-bottom: 0px;
      padding-left: 10px;
      padding-right: 10px;
      padding-top: 25px;
      z-index: 2;
      background-color: transparent;
      @extend .bloc--text-content;
      transition: all 250ms ease 0;
      height: 95px;
      padding-top: 15px;
      padding-bottom: 0;
      justify-content: flex-start;
      @include media-breakpoint-down(md){
        padding-left: 0;
        padding-right: 0;
        height: auto;
      }
      &:before{
        content:'';
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        background-color: $color__white;
        top: 0;
        left: 0;
        z-index: -1;
        transition: $transition-base;
      }
      .tag{
        position: absolute;
        top: 0;
        bottom: auto;
        left: 50%;
        transform: translate(-50%, -50%);
        transition: all 250ms ease;
        @include media-breakpoint-down(sm){
          position: relative;
          width: 100%;
          margin-top: -3px;
        }
      }
    }
    &:hover{
      .card__visuel{
        background-color: $color__secondary;
      }
    }
  }
  &--formule,
  &--soin,
  &--spa{
    &:hover{
      .card__visuel{
        &::before{
          opacity: 1;
        }
      }
      // .card__content{
      //   &:before{
      //     transform: scaleY(0);
      //     opacity: 0;
      //   }
      //   *{
      //     color: $color__white;
      //   }
      //   .text--category{
      //     color: $color__primary;
      //     opacity: 1;
      //   }
      //   .tag{
      //     opacity: 0;
      //     transform: translate(-50%, -80%);
      //   }
      // }
    }
  }
  &--spa{
    .card__visuel:before{
      background-color: $color__primary;
    }
  }
  &--formule,
  &--cadeau,
  &--soin-expanded{
    padding-bottom: 172px;
    background-color: $color__white;
    transition: all 250ms ease;

    @include media-breakpoint-down(md){
      padding-bottom: 0;
    }
    .card__content{
      height: auto;
      .button{
        margin: 13px auto 0;
        &:before{
          background-color: $color__primary;

        }
      }
      >a:not(.link){
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }
      .link{
        margin-top: 20px;
        margin-bottom: 12px;
        @include media-breakpoint-down(sm){
          padding: 7px;
          letter-spacing: 1px; 
          color: $color__secondary;
          border: 1px solid $color__secondary;
          overflow: hidden;
          position: relative;
          display: flex;
          max-width: 100%;
          width: 100%;
          text-transform: uppercase;
          font-size: $fontSize__extra-extra-small;
          cursor: pointer;
          z-index: 1;
          background-color: $color__white;
          margin-bottom: 0;
          margin-top: 7px;
          justify-content: center;
          &:before{
            display: none;
          }
        }
      }
    }
    &:hover{
      // background-color: $color__secondary;
      .button{
        @extend .button--white;
        // background-color: $color__secondary;
        border-color: $color__primary;
        color: $color__white;
      }
    }
  }
  &--soin-expanded{
    @include media-breakpoint-down(sm){
      a{
        max-width: 100%;
        width: 100%;
        .button{
          color: $color__white;
          border-color: $color__primary;
          background-color: $color__primary;
          margin: 0 !important;
          width: 100%;
          max-width: 100%;
          padding: 7px !important;
          margin-top: 10px !important;
          input{
              color: $color__white;
          }
        }
      }
    }
  }
  &--formule{
    margin-top: 50px;
    padding-bottom: 80px;
    @include media-breakpoint-down(sm){
      margin-left: -15px;
      margin-right: -15px;
      margin-top: 30px;
      padding-bottom: 0;
      .card__content__wrapper{
        position: relative;
        margin-top: -60px;
        padding: 0 20px;
      }
    }
    .card__visuel{
      &:before{
        background-color: $color__primary;
      }
    }
    .card__content{
      max-width: 308px;
      @include media-breakpoint-down(sm){
        padding: 20px;
        max-width: 100%;
        border: 1px solid $color__primary;
        text-align: left;
        &>a:not(.link){
          align-items: flex-start;
        }
      }
      .price{
        margin-bottom: 15px;
        @include media-breakpoint-down(sm){
          position: absolute;
          top: 0;
          right: 0;
          margin: 28px 15px 0;
          font-size: $fontSize__big-minus;
        }
      }
      .card__title{
        font-size: $fontSize__big-minus;
        line-height: $fontSize__big-minus + 0.3rem;
        text-transform: uppercase;
        // color: $color__secondary;
        opacity: 1;
        letter-spacing: 0.92px;
        @include media-breakpoint-down(sm){
          margin-bottom: 15px;
        }
      }
      .text--category,
      .card__title{
        @include media-breakpoint-down(sm){
          max-width: calc( 100% - 60px);
        }
      }
      .button{
        &:hover{
          color: $color__white;
        }
        @include media-breakpoint-down(sm){
          max-width: 100%;
          width: 100%;
          
        }
      }
      .link{
        color: $color__secondary;
        display: block;
        margin-left: 50px;
        margin-right: 50px;
        font-size: $fontSize__extra-small;
        line-height: $fontSize__extra-small + .3rem;
        text-transform: uppercase;
        letter-spacing: 0.93px;
        &:hover{
          color: $color__white;
        }
        @include media-breakpoint-down(sm){
          max-width: 100%;
          width: 100%;
          padding: 10px 15px;
          line-height: 22px;
          &:before{
            display: none;
          }
        }
      }
    }
    &:hover{
      .link{
        color: $color__primary;
      }
    }
  }
  &--cadeau-title-mobile{
    @extend .title--highlight;
    margin: 0 !important;
    margin-top: -1px !important;
    border-bottom: 1px solid $color__grey--extra-light;
    border-top: 1px solid $color__grey--extra-light;
    padding: 14px 0;
    display: none;
    align-items: center;
    justify-content: space-between;
    @include media-breakpoint-down(sm){
      display: flex;
    }
    &:after{
      content: "+";
      padding: 0 10px;
      display: flex;
      font-weight: $fontWeight__semi-bold;
      font-size: $fontSize__small;
      color: $color__primary;
    }
    &.active{
      &:after{
        content: "-";
      }
    }
  }
  &--cadeau{
    padding-bottom: 0;
    @include media-breakpoint-down(sm){
      margin-top: 0;
      margin-bottom: 0;
      display: none;
      border-bottom: 1px solid $color__grey--extra-light;
    }
    .card__content__wrapper{
      position: relative;
      padding: 0 20px;
      @include media-breakpoint-down(sm){
        margin-top: -60px;
        padding: 0 20px;
      }
      .card__content{
        max-width: 376px;
        margin-top: -80px;
        padding: 35px 24px 0 24px;
        @include media-breakpoint-down(sm){
          margin-top: 0;
          padding: 20px;
        }
        .card__title{
          font-size: $fontSize__big;
          line-height: $fontSize__big + 1.4rem;
          text-transform: uppercase;
        }
        .description{
          padding-top: 40px;
          @include media-breakpoint-down(sm){
            padding-top: 0;
          }
        }
        .text--category{
          opacity: 1;
        }
        .card__additional{
          opacity: 0.6;
        }
        .card__additional + .description{
          padding-top: 16px;
        }
      }
    }
    &:hover{
      background-color: $color__white;
    }
  }
  &--height-2x{
    @include media-breakpoint-up(lg){
      // min-height: 651.12px;
    }
    .card__content{
      margin-bottom: 123px;
    }
  }
  &--width-2x{
    @include media-breakpoint-up(lg){
      // min-height: 310.56px;
    }
  }
  &--product{
    padding-bottom: 0;
    .card__content{
      .card__title + p{
        margin-top: 20px;
      }
      .link{
        margin-bottom: 20px;
      }
    }
  }
  &--resultats{
    .card__visuel{
      picture{
        width: max-content;
        max-width: 100%;
        display: block;
        margin: 0 auto;
      }
    }
    .link{
      border-bottom: 1px solid $color__secondary;
      padding-bottom: 10px;
      margin-bottom: 20px;
    }
    .card__content{
      .row{
        width: 100%;
      }
    }
    .resultat-prouve{
      margin-top: 0;
      margin: 30px 0;
      @include media-breakpoint-down(sm){
        flex-direction: column;
        p{
          padding-left: 0;
          text-align: center;
        }
      }
    }
  }
  &--product--routine-beaute{
    height: calc(100% - 20px );
    padding-bottom: 0;
    h3{
      @extend .title--routine;
      text-align: center;
      padding-bottom: 56px;
      font-size: $fontSize__big;
      line-height: $fontSize__big + .2rem;
      font-family: $font__secondary;
      @include dot(30px);
      transition: $transition-base;
      @include media-breakpoint-down(sm){
        font-size: $fontSize__medium-plus;
        line-height: $fontSize__medium-plus;
        padding-bottom: 25px;
        margin-bottom: 0;
        &:after{
          bottom: 10px;
        }
      }
      small{
        font-size: $fontSize__small-plus;
        @include media-breakpoint-down(sm){
          font-size: $fontSize__default--mobile;
        }
      }
    }
    .card__content__wrapper{
      flex-grow: 2;
      .card__content{
        height: 100%;
      }
    }
    .card__visuel,
    .card__content{
      border: 1px solid $color__white;
      transition: $transition-base;
    }
    .card__visuel{border-bottom: 0;}
    .card__content{border-top: 0;}
    &.actif,
    &:hover{
      h3{
        color: $color__primary;
      }
      .card__visuel,
      .card__content{
        border-color: $color__primary;
        img{
          transform: none;
        }
      }
    }
    &.nuit{
      &.actif,
      &:hover{
        .card__visuel,
        .card__content{
          border-color: $color__secondary;
          background-color: $color__secondary;
          .text--category,
          .card__title{
            color: $color__white;
          }
        }
      }
    }
  }
  &--reassurance{
    @extend %card--border;
    padding: 58px 40px 53px;
    height: 100%;
    margin: 0;
    justify-content: flex-end;
    // @include media-breakpoint-down(md){
    //   margin: 15px 0;
    //   height: auto;
    // }
    @include media-breakpoint-down(md){
      padding: 0;
      justify-content: space-between;
      background-color: $color__white !important;
      padding-top: 20px;
      &:after{
        display: none;
      }
    }
    .card__visuel{
      display: flex;
      justify-content: center;
      @include media-breakpoint-down(md){
        width: 70px;
        height: 70px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        margin: 0 auto;
        margin-bottom: 10px;
      }
      i.icon{
        color: $color__white;
        margin-bottom: 25px;
        @include media-breakpoint-down(md){
          font-size: 40px;
          height: 40px;
          width: 40px;
          margin-bottom: 0;
        }
      }
    }
    .card__content__wrapper{
      @include media-breakpoint-down(md){
        flex-grow: 2;
      }
    }
    .card__content{
      padding: 0;
      .card__title, .card__title span{
        text-transform: uppercase;
        font-family: $font__main;
        font-size: $fontSize__medium;
        margin-bottom: 10px;
        span{
          margin-bottom: 0;
        }
        @include media-breakpoint-down(md){
          font-size: $fontSize__extra-extra-small;
          line-height: $fontSize__extra-extra-small + .2rem;
          font-weight: $fontWeight__default;
        }
      }
      .card__title, .card__title span, p{
        color: $color__white;
        @include media-breakpoint-down(md){
          color: $color__secondary;
        }
      }
      .button{
        margin-top: 26px;
      }
      @include media-breakpoint-down(md){
        p, .button{
          display: none;
        }
      }
    }
    &.card--primary{
      background-color: $color__primary;
      @include media-breakpoint-down(md){
        .card__visuel{
          background-color: $color__primary;
        }
      }
      &:after{
        background-color: $color__tertiary;
      }
    }
    &.card--secondary{
      background-color: $color__secondary;
      @include media-breakpoint-down(md){
        .card__visuel{
          background-color: $color__secondary;
        }
      }
      &:after{
        background-color: $color__primary;
      }
    }
    &.card--tertiary{
      background-color: $color__tertiary;
      @include media-breakpoint-down(md){
        .card__visuel{
          background-color: $color__tertiary;
        }
      }
      &:after{
        background-color: $color__secondary;
      }
    }
    &.card--quaternary{
      background-color: $color__quaternary;
      @include media-breakpoint-down(md){
        .card__visuel{
          background-color: $color__quaternary;
        }
      }
      &:after{
        background-color: $color__secondary;
      }
    }
    &:hover{
      .button{
        @extend .button--white:hover;
      }
    }
  }
  &--no-border{
    &:after{display: none;}
  }
  &--border{
    height: calc( 100% - (#{$card__marging}*2) );
    .card__content{
      border: 1px solid $color__secondary;
      height: 100%;
      display: flex;
      justify-content: space-between;
      padding: 50px 36px 36px;
      align-items: flex-start;
      // margin-bottom: 23px;
      blockquote .icon-guillemet{
        margin-bottom: 10px;
      }
    }
  }
  &--no-marge{
    margin: 0;
    height: 100%;
  }
  &--content-left{
    .card__content{
      @include media-breakpoint-up(lg){
        left: 20px;
        right: auto;
      }
    }
  }

  &--content-right{
    .card__content{
      @include media-breakpoint-up(lg){
        left: auto;
        right: 20px;
      }
    }
  }
  &--titleBig{
    .card__content .card__title{
      @extend .title--h2-small;
      @include media-breakpoint-down(sm){
        margin-top: 20px;
      }
    }
  }
}