@import url('https://fonts.googleapis.com/css?family=Baskerville:400,700,400i|Montserrat:300,400,700&display=swap');

$color__main: #E3A599;
$color__primary: #C0A76E;
$color__secondary: #394147;
$color__tertiary: #586059;
$color__quaternary: #488187;

$color__institut : #EE836F;
$color__day-spa : $color__primary;
$color__spa-hotelier : $color__quaternary;

$color__black: #000;
$color__dark: #151515;
$color__grey--dark: #979797;
$color__grey: #C0C0C0;
$color__grey--light :#D8D8D8;
$color__grey--extra-light2: #E1E1E1;
$color__grey--extra-light: #f5f5f5;
$color__white: #fff;
// Used for generating CSS utility classes
$colors: (
  'main': $color__main,
  'primary': $color__primary,
  'secondary': $color__secondary,
  'tertiary': $color__tertiary,
) !default;

$color__text: $color__secondary;


$fontSize__breadcrumb: 0.9rem;
$fontSize__extra-extra-small: 1.1rem;
$fontSize__extra-small: 1.2rem;
$fontSize__default--mobile: 1.2rem;
$fontSize__default: 1.3rem;
$fontSize__small: 1.4rem;
$fontSize__small-plus: 1.6rem;
$fontSize__medium: 1.8rem;
$fontSize__medium-plus: 2rem;
$fontSize__big-minus: 2.2rem;
$fontSize__big: 2.4rem;
$fontSize__big-plus: 2.6rem;
$fontSize__large: 3rem;

$fontSize__title-H1: 3.8rem;
$fontSize__title-H1--mobile: 2.5rem;

$fontSize__title-H2: 3.8rem;
$fontSize__title-H2--mobile: 2.4rem;

$fontSize__title-H3: 2rem;
$fontSize__title-H3--mobile: 1.3rem;

$fontSize__title-H4: 1.4rem;


$fontWeight__extra-light: 100;
$fontWeight__light: 300;
$fontWeight__default: 400;
$fontWeight__semi-bold: 600;
$fontWeight__bold: 700;
// $font__main: 'Raleway', sans-serif;
$font__main: 'Montserrat', sans-serif;
$font__secondary: 'Baskerville', serif;

$header__height-default: 223px;
$header__height-fixed: 122px;
$header__height-tablet: 122px;
$header__height-mobile: 44px;

// Transition
$transition-base: all 250ms ease-in-out;
$shadow-base: 0 10px 10px 0 rgba(0,0,0,.1);
