.article{
    padding: 15px 0;
    display: flex;
    flex-wrap: wrap;
    .article__visuel{
        overflow: hidden;
        @extend %responsive--visuel-fullsize;
        @include media-breakpoint-down(sm){
            margin-bottom: 18px;
        }
        img{
            transition: transform 1000ms ease;
            width: 100%;
        }
    }
    .article__content{
        .article__title{
            margin-bottom: 15px;
        }
        .article__title, p{
            color: $color__secondary;
        }
        .text--category{
            margin-bottom: 10px;
        }
    }
    &:not(.article--large){
        border-bottom: 1px solid $color__primary;
        padding-bottom: 40px;
        margin-bottom: 15px;
        .article__visuel{
            margin-bottom: 18px;
            width: 100%;
        }
        .article__title{
            @include media-breakpoint-up(md){
                font-family: $font__secondary;
                font-size: $fontSize__medium;
                line-height: $fontSize__medium + 1rem;
                font-weight: $fontWeight__bold;
            }
        }
    }
    &--large{
        flex-direction: row-reverse;
        @include media-breakpoint-down(sm){
            .article__content{
               border-bottom: 1px solid $color__primary;
                padding-bottom: 40px;
            }
        }
    }
    &:hover{
        .article__visuel{
            img{
                transform: scale(1.1);
            }
        }
        .button{
            @extend .button:hover;
        }
    }
}