.account__access_wrapper{
    display: none;
    position: absolute;
    z-index: 20;
    width: 280px;
    right: -40px;
    top: calc( 100% - 5px );
    .account__access_inner{
        background-color: $color__white;
        padding: 20px;
        box-shadow: 0px -5px 20px 0 rgba(0,0,0,.1);
        margin-top: 30px;
        position: relative;
        a.button{
            width: 100%;
            max-width: 100%;
            justify-content: center;
        }
        a + a{
            margin-top: 20px;
        }
        &:before{
            content: "";
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 0 15px 20px 15px;
            border-color: transparent transparent #ffffff transparent;
            position: absolute;
            top: -13px;
            right: 35px;
        }
    }
}
.account__access_button{
    &:hover{
        .account__access_wrapper{
            display: block;
        }       
    }
}