.responsive-bg{
    background-size: cover;
}

%responsive--section-fullsize{
    @include media-breakpoint-down(sm){
        &:not(.banner){
            .container{
                max-width: 100%;
                padding: 0;
                .row{
                    margin-left: 0;
                    margin-right: 0;
                    .col-12{
                        padding: 0;
                    }
                }
            }
        }
    }
}
%responsive--visuel-fullsize{
    @include media-breakpoint-down(sm){
        margin: 0 -15px !important;
        width: 100vw !important;
    }
}