.choixFormule__receiver{
    position: relative;
    > .section{
        transition: all 500ms ease 0ms;
        opacity: 1;
        background-color: $color__white;
    }
    &.hide > .section{
        transform: scale(1) translateY(500px);
        opacity: 0;
        transition-delay: 0ms;
    }
    &.loaded > .section{
        transform: scale(1) translateY(-500px);
        opacity: 0;
        transition-delay: 0;
    }
}
.choixFormule{
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
    &__overlay,
    &__loader,
    &__content{
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        top: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
        transition: all 500ms ease 250ms;
        z-index: 1;
    }
    &__overlay{
        background-color: rgba($color__black, .7);
        display: none;
    }
    &__loader{
        display: flex;
        justify-content: center;
        align-items: center;
        transition-delay: 500ms;
    }
    &__content{
        position: absolute;
        transform: scale(1) translateY(-500px);
        background-color: $color__white;
        .choixFormule__inner{
            background-color: $color__white;
            display: flex;
            width: 100%;
            .button{
                margin: 0 auto;
            }
            .storelocator__show{
                @include media-breakpoint-down(md){
                    margin-top: 10px;
                    text-align: center;
                }
            }
            .section{
                width: 100%;
            }
            .step{
                margin: 0 auto;
                display: block;
                @extend .text--highlight;
                opacity: 1;
                text-align: center;
                margin-bottom: -15px;  
            }
            h2.storelocator__show{
                max-width: 600px;
                margin: 0 auto;
                margin-top: 35px;
                text-align: center;
                .icon{
                    display: block;
                    margin: 0 auto;
                    color: $color__secondary;
                    margin-bottom: 10px;
                }
            }
            .formule__info{
                display: flex;
                align-items: center;
                margin-bottom: 50px;
                @include media-breakpoint-down(md){
                    align-items: flex-start;
                    margin-top: 20px;
                    margin-bottom: 40px;
                }
                .formule__visuel{
                    max-height: 160px;
                    overflow: hidden;
                    width: 270px;
                    min-width: 270px;
                    @include media-breakpoint-down(md){
                        width: 30%;
                        min-width: 30%;
                        max-height: 100%;
                    }
                    picture{
                        display: block;
                        transform: translateY(-25%);
                        max-width: 100%;
                        @include media-breakpoint-down(md){
                            transform: translate(0);
                        }
                    }
                }
                .formule__content{
                    padding-left: 90px;
                    @include media-breakpoint-down(md){
                        padding-left: 15px;
                    }
                    .text--category,
                    .title{
                        display: block;
                    }
                    .title{
                        font-family: $font__secondary;
                        font-weight: $fontWeight__bold;
                        font-size: $fontSize__big-minus;
                        color: $color__primary;
                        text-transform: none;
                        @include media-breakpoint-down(md){
                            font-size: $fontSize__small-plus;
                            line-height: $fontSize__small-plus + .4rem;
                        }
                    }
                    .link{
                        color: $color__secondary;
                        &:hover{
                            color: $color__primary;
                        }
                    }
                }
            }
        }
        .choixFormule__close{
            margin-top: 20px;
            // margin: 20px 0;
            // position: absolute;
            // left: 10px;
            // right: 10px;
            display: flex;
            cursor: pointer;
            align-items: center;
            font-size: $fontSize__extra-small;
            letter-spacing: 0.93px;
            text-transform: uppercase;
            @include media-breakpoint-down(md){
                margin-bottom: 20px;
                justify-content: center;
            }
            i{
                color: $color__secondary;
                font-size: 1rem;
            }
        }
    }
    &.show{
        z-index: 1;
        .choixFormule__overlay,
        .choixFormule__loader{
            opacity: 1;
        }
    }
    &.loaded{
        .choixFormule__loader{
            opacity: 0;
        }
        .choixFormule__content{
            opacity: 1;
            transform: scale(1);
        }
    }

    &.hide{
        .choixFormule__overlay{
            opacity: 0;
            transition-delay: 500ms;
        }
        .choixFormule__content{
            opacity: 0;
            transform: scale(1) translateY(500px);
        }
    }
}