
.section{
  margin: 15px 0;
  @include media-breakpoint-up(md){
    margin: 44px 0;
  }
  &.section--large{
    @include media-breakpoint-up(md){
      margin: 92px 0;
    }
  }
  &.parallax-bg{
    // background-attachment: fixed;
    background-size: cover;
    // transition: all 10ms ease-in-out;
  }
  .col-sep{
    &:before{
      content: "";
      width: 1px;
      height: 100%;
      display: block;
      background-color: $color__primary;
      position: absolute;
      top: 0;
      left: 50%;
    }
  }
  &--no-marge-md{
    @include media-breakpoint-down(md){
      margin: 0;
    }
  }
  &--margeTop-small{
    margin-top: 14px;
  }
  &--no-marge{
    margin: 0;
  }
  
  &--jardins{
    @include media-breakpoint-down(sm){
      text-align: center;
      .button{
        margin: 0 auto;
        max-width: max-content;
      }
    }
  }
  &--white{
    height: 400px;
    display: flex;
    align-items: center;
    @include media-breakpoint-down(sm){
      height: 220px;
    }
    *{
      color: $color__white;
    }
  }
  &--tutos{
    .tutos__visuels--main{
      @include media-breakpoint-down(sm){
        margin-bottom: -90px;
        margin-left: -15px;
      }
      @include media-breakpoint-up(md){
        padding: 0 50px 0 100px;
      }
    }
    .tutos__visuels--secondary{
      max-width: 310px;
      box-shadow: 10px -10px 0 10px $color__white;
      position: relative;
      display: none;
      @include media-breakpoint-up(md){
        margin-top: -30px;
        display: block;
      }
      @include media-breakpoint-up(lg){
        margin-top: -160px;
      }
    }
    .tutos__content{
      position: relative;
      height: 100%;
      @include media-breakpoint-down(sm){
        margin-left: 50px;
        padding: 15px;
        background-color: $color__white;
      }
      .icon-guillemet{
        font-size: 22px;
        margin-bottom: 30px;
        display: inline-block;
        @include media-breakpoint-down(sm){
          margin-bottom: 0;
          font-size: 18px;
        }
      }
      .text--category{
        transform: rotate(-90deg);
        transform-origin: top left;
        font-weight: $fontWeight__bold;
        position: absolute;
        left: -45px;
        bottom: 25px;
        @include media-breakpoint-down(sm){
          display: none;
        }
      }
      h3, blockquote{
        margin-bottom: 35px;
        @include media-breakpoint-down(sm){
          margin-bottom: 20px;
        }
      }
      .button{
        margin-top: 42px;
      }
    }
  }
  &--reseaux-sociaux{
    @include media-breakpoint-down(sm){
      .align-self-center{
        align-self: initial !important;
      }
      img + img {
        display: none;
      }
      .col-4{
        padding: 2px;
      }
    }
    img{
      width: 100%;
      height: auto;
    }
    .img-padding{
      @include media-breakpoint-up(md){
        padding-top: 34px;
      }
    }
    .reseaux-sociaux__content{
      @extend .bloc--text-content;
      padding: 0;
      @include media-breakpoint-up(md){
        padding: 37px 64px 37px;
        margin-top: -159px;
      }
      span,b{
        opacity: 1;
        font-weight: $fontWeight__default;
      }
      .title--border{
        letter-spacing: 1px;
        text-transform: uppercase;
        margin-top: 5px;
        font-size: $fontSize__extra-small;
      }
    }
    .list--rs{
      li{
        margin: 12px 13px 16px;
        @include media-breakpoint-down(sm){
          margin-top: 25px;
          margin-bottom: 10px;
        }
      }
    }
  }
  &--banner-homepage{
    margin: 0;
    .banner{
      @extend %banner-white--mobile;
      .banner__wrapper{
        .col-12{
          padding-left: 15px !important;
          padding-right: 15px !important;
        }
      }
    }
  }
  &--reassurances{
    .col-12:nth-child(1) .card{
      transition-delay: 250ms;
    }
    .col-12:nth-child(3) .card{
      transition-delay: 150ms;
    }
  }
  &--banner{
    @extend %responsive--section-fullsize;
    &.banner{
      height: auto;
      margin: 30px 0 !important;
    }
  }
  &--category-filters{
    @include media-breakpoint-down(sm){
      margin-top: 0;
      .filters{
        margin-bottom: 15px;
      }
    }
  }
  &--categories{
    @extend %responsive--section-fullsize;
    @include media-breakpoint-down(sm){
      margin-top: 0;
    }
    &.banner{
      height: auto;
      margin: 30px 0 !important;
    }
    .grid-item{
      .card.even{
        @extend %card--reverse-row;
      }
    }
  }
  &--product-information{
    margin-top: 0;
    @include media-breakpoint-down(sm){
      margin-bottom: 0;
      h1{
        margin-top: 5px;
        margin-bottom: 17px;
      }
    }
    .price{
      font-size: $fontSize__medium;
      letter-spacing: 1.29px;
      font-weight: $fontWeight__bold;
      @include media-breakpoint-down(sm){
        font-size: $fontSize__small-plus;
      }
      & + .complement{
        padding-left: 15px;
        &:before{
          content: "-";
          padding-right: 15px;
          @include media-breakpoint-down(sm){
            padding-right: 5px;
          }
        }
      }
    }
    .complement{
      font-size: $fontSize__medium;
      color: $color__grey;
      letter-spacing: 1.29px;
      @include media-breakpoint-down(sm){
        padding-left: 5px;
        font-size: $fontSize__small-plus;
      }
    }
    .colors{
      @extend .list--horizontal;
      flex-wrap: wrap;
      margin-top: 35px !important;
      @include media-breakpoint-down(sm){
        justify-content: space-around;
        margin-top: 25px !important;
        order: 3;
      }
      li{
        a{
          display: block;
          width: 22px;
          height: 22px;
          border-radius: 50%;
          margin: 15px 10px;
          transition: all 250ms ease;
          position: relative;
          &:after{
            content: "";
            position: absolute;
            top: 0px;
            left: 0px;
            // transform: translate(-50%, -50%);
            width: 22px;
            height: 22px;
            border: 1px solid $color__tertiary;
            border-radius: 50%;
            opacity: 0;
            transition: all 250ms ease;
          }
          &.active,
          &:hover{
            transform: scale(1.2);
            &:after{
              top: -3px;
              left: -3px;
              width: 28px;
              height: 28px;
              opacity: 1;
            }
          }
        }
      }
      & + .features{
        margin-top: 35px;
      }
    }
    .features{
      margin: 70px 0 16px;
      @include media-breakpoint-down(sm){
        border-top: 0;
        li{
          flex-direction: column;
          text-align: center;
          border-bottom: 0;
          padding: 10px 0;
          &:not(.hidden--mobile) + li {
            position: relative;
            &:before{
              content: "";
              width: 30px;
              height: 1px;
              background-color: $color__primary;
              position: absolute;
              top: 0;
              left: 50%;
              transform: translateX(-50%);
            }
          }
          strong,span{
            width: 100%;
          }
        }
      }
      li.color{
        span{
          font-weight: $fontWeight__semi-bold;
          letter-spacing: 0.93px;
        }
      }
    }
    .list--rs{
      justify-content: flex-end;
      li{
        padding: 0 8px;
      }
    }
    .button__wrapper{
      display: flex;
      
      .button{
        margin-top: 27px;
        & + .button{
          margin-left: 20px;
        }
      }
    }
    .carousel{
      padding: 0 80px;
      @include media-breakpoint-down(sm){
        padding: 0;
        .carousel__pagination{
          position: absolute;
          bottom: 0;
        }
      }
      .carousel__inner{
        border: 1px solid $color__primary;
        @include media-breakpoint-down(sm){
          border: 0;
        }
      }
      .carousel__item{
        picture{
          display: block;
        }
      }
      .carousel__nav a{
        width: 80px;
      }
    }
    .link{
      display: flex;
      align-items: center;
    }
    form{
      fieldset{
        > div:not(.form__item){
          display: flex;
          > * + *{
            margin-left: 20px;
          }
        }
        .form__item{
          width: auto;
          select{
            width: auto;
          }
          &.form__item--radio{
            @include media-breakpoint-down(sm){
              margin-top: 10px;
            }
          }
        }
        .form__footer{
          margin-top: 20px;
          @include media-breakpoint-down(sm){
            margin-top: 0;
          }
        }
      }
    }
    @include media-breakpoint-down(md){
      form{
        display: flex;
        flex-direction: column;
        padding-top: 15px;
        .button__wrapper{
          order: 2;
          flex-direction: column;
          .button{
            padding: 10px;
            margin-top: 20px;
            // max-width: 100%;
            // width: 100%;
            justify-content: center;
            margin: 25px auto 0 !important;
          }
        }
        .features{
          margin-top: 20px;
          order: 3;
          margin-bottom: 0;
        }
      }
    }
    .product-information-pictos{
      display: flex;
      order: 3;
      ul{
        @extend .list--horizontal;
        justify-content: space-around;
        margin-top: 50px !important;
        margin-bottom: 10px !important;
        width: 100%;
        padding-left: 80px;
        @include media-breakpoint-down(sm){
          flex-wrap: wrap;
          margin-top: 20px !important;
          padding-left: 0;
        }
        li{
          display: flex;
          flex-direction: column;
          align-items: center;
          max-width: 120px;
          @include media-breakpoint-down(sm){
            width: calc( 100% / 3);
            flex-basis: calc( 100% / 3);
            min-width: calc( 100% / 3);
            margin: 10px 0;
          }
          i{
            color: $color__secondary;
            font-size: 4.4rem;
            height: 44px;
            width: 44px;
            @include media-breakpoint-down(sm){
              color: $color__primary;
            }
          }
          span{
            @extend .text--highlight;
            line-height: 1.6rem;
            opacity: 1;
            text-align: center;
            margin-top: 10px;
            @include media-breakpoint-down(sm){
              color: $color__primary;
            }
          }
        }
      }
    }
  }
  &--breadcrumb{
    @include media-breakpoint-down(sm){
      display: none;
    }
  }
  &--breadcrumb--fixed{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 2;
  }
  &--spas-details{
    @include media-breakpoint-down(sm){
      background-color: $color__white;
      position: relative;
      margin: 0 20px;
      margin-top: -80px;
      .text--highlight{
        margin-top: 10px;
      }
      .title--margin{
        margin-bottom: 20px;
      }
    }
  }
  &--product-details{
    @include media-breakpoint-down(sm){
      margin-top: 0;
    }
    .section__wrapper{
      @include media-breakpoint-up(md){
        border: 1px solid $color__primary;
        border-top: 0;
        border-right: 0;
        padding: 40px;
        position: relative;
        &:after{
          content: "";
          width: 1px;
          height: 115px;
          background-color: $color__primary;
          position: absolute;
          bottom: 0;
          right: 0;
        }
      }
      h3{
        @extend .title--highlight;
        margin-bottom: 1.5rem;
        @include media-breakpoint-down(sm){
          display: block;
          margin: 0 !important;
          border-bottom: 1px solid $color__grey--extra-light;
          padding: 14px 0;
          display: flex;
          justify-content: space-between;
          &:after{
            content: "+";
            padding: 0 10px;
            display: block;
            font-weight: $fontWeight__semi-bold;
            font-size: $fontSize__small-plus;
            color: $color__primary;
          }
          &.active{
            margin-bottom: 10px !important;
            &:after{
              content: "-";
            }
          }
        }
      }
      a{
        @extend .link;
      }
      p {
        @include media-breakpoint-down(sm){
          display: none;
        }
        & + h3{
          margin-top: 3rem;
        }
        & + ul{
          margin-top: -2rem;
        }
        a{
          display: inline-block;
        }
      }
      ul{
        @extend .list--light;
        @include media-breakpoint-down(sm){
          display: none;
        }
      }
      .col-12:first-child{
        @include media-breakpoint-up(md){
          margin-top: -53px;
          h3:first-child{
            margin-bottom: 2.9rem;
            padding-left: 57px;
            position: relative;
            &:before{
              content: "";
              width: 78px;
              height: 1px;
              background-color: $color__primary;
              position: absolute;
              top: 50%;
              left: -41px;
            }
          }
        }
      }
      .conseils{
        &:before{
          content: "";
          width: 450px;
          height: 1px;
          background-color: $color__primary;
          display: block;
          margin: 0 auto;
          max-width: 100%;
          margin-bottom: 40px;
          margin-top: 20px;
          @include media-breakpoint-down(sm){
            display: none;
          }
        }
        p:last-child{
          margin-bottom: 0;
        }
      }
    }
  }
  &--video-scroll{
    .video-scroll__wrapper{
      display: flex;
      align-items: flex-end;
      @include media-breakpoint-down(md){
        flex-direction: column;
        flex-direction: column-reverse;
      }
      .video-scroll__content{
        width: 45%;
        margin-right: -15%;
        position: relative;
        z-index: 2;
        background-color: $color__white;
        padding: 30px 40px;
        text-align: right;
        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
        flex-direction: column;
        @include media-breakpoint-down(md){
          width: 100%;
          margin-right: 0;
          align-items: flex-start;
          padding: 10px 20px;
          text-align: left;
        }
      }
      .video-scroll{
        width: 70%;
        @include media-breakpoint-down(md){
          width: 100%;
        }
      }
    }
  }
  &--video-hover{
    overflow: hidden;
  }
  &--product-actifs{
    @include media-breakpoint-down(sm){
      h3,p{
        display: block;
        text-align: center;
      }
    }
    h3{
      @extend .title--highlight;
    }
    img{
      display: block;
      width: 100%;
      height: auto;
      @include media-breakpoint-down(sm){
        margin-bottom: 30px;
      }
      & + img{
        margin-top: 30px;
      }

    }
  }
  &--products-selection{
    .filters{
      @extend .list--horizontal;
      display: flex;
      justify-content: center;
      margin-bottom: 30px !important;
      @include media-breakpoint-down(sm){
        justify-content: space-between;
        margin-bottom: 10px !important;
      }
      li{
        padding: 0 35px;
        @include media-breakpoint-down(sm){
          padding: 0;
        }
        a{
          @extend .text--category;
          opacity: 1;
          position: relative;
          &:after{
            content: "";
            width: 100%;
            height: 1px;
            background-color: $color__primary;
            display: block;
            position: absolute;
            bottom: 0px;
            left: 0;
            transform-origin: 50%;
            transform: scaleX(0);
            transition: $transition-base;
          }
          &:hover,
          &.actif{
            color: $color__primary;
            @include media-breakpoint-down(sm){
              font-weight: $fontWeight__bold;
            }
            &:after{
              transform: scaleX(1);
            }
          }
        }
      }
    }
    #products-selection__wrapper{
      transition: all 500ms ease;
    }
  }
  &--produit{
    .card{
      @include media-breakpoint-down(sm){
        .card__content{
          .button{
            max-width: 60%;
            width: 100%;
          }
        }
      }
      .card__visuel{
        @extend %responsive--visuel-fullsize;
      }
    }
  }
  &--text{
    blockquote{
      text-align: center;
      .icon{
        margin-bottom: 10px;
        &:last-child{
          transform: rotate(180deg);
          margin-top: 10px;
          margin-bottom: 0;
          align-self: flex-end;
        }
      }
    }
    @include media-breakpoint-down(sm){
      .row .col-12:not(.section--text__title__wrapper){
        order: 1;
      }
    }
    .title--border{
      @include media-breakpoint-down(sm){
        // margin-bottom: 20px;
      }
    }
    .section--text__wrapper{
      @include media-breakpoint-down(sm){
        p{
          // text-align: center;
        }
      }
    }
    .text--highlight{
      @include media-breakpoint-down(sm){
        opacity: 1;
      }
    }
    h3{
      @include media-breakpoint-down(sm){
        margin-top: 10px;
        margin-bottom: 20px;
      }
    }
    .section--text__visuel__wrapper{
      @include media-breakpoint-down(sm){
        order: 0 !important;
      }
    }
    .section--text__visuel{
      display: flex;
      flex-direction: column;
      align-items: center;
      @extend %responsive--visuel-fullsize;
      img{
        @include media-breakpoint-down(sm){
          margin: 15px 0;
        }
      }
      .button{
        margin-top: 50px;
        @include media-breakpoint-down(sm){
          margin: 10px auto 10px;
          max-width: 60%;
          width: inherit;
        }
      }
    }
    .section--text--white{
      background-color: $color__white;
      padding: 0;
      z-index: 2;
      position: relative;
      @include media-breakpoint-up(lg){
        padding: 28px 30px;
      }
    }
    .section--text--shifted-right{
      @include media-breakpoint-up(lg){
        transform: translateX(50px);
        .button{
          margin-right: 60px;
        }
      }
    }
    .section--text--shifted-left{
      @include media-breakpoint-up(lg){
        transform: translateX(-50px);
      }
    }
    .section--text--shifted-bottom{
      @include media-breakpoint-up(lg){
        margin-top: 100px;
      }
    }
    .section--text--shifted-top{
      @include media-breakpoint-up(lg){
        margin-top: -100px;
      }
    }
    .section--text--extend-left{
      @include media-breakpoint-up(lg){
        width: calc( 100% + 100px );
        transform: translateX(-100px);
        .button{
          margin-left: 100px;
        }
      }
    }
    .section--text--extend-right{
      @include media-breakpoint-up(lg){
        width: calc( 100% + 100px );
      }
    }
    &.section--big-title{
      .section--text--content{
        h3{
          @extend .title--sep;
          text-transform: uppercase;
          padding-bottom: 0;
          margin-bottom: 20px;
          display: block;
          @include media-breakpoint-down(sm){
            margin-top: 20px;
          }
          &:after{
            display: none;
          }
        }
      }
    }
    &:not(.section--big-title):not(.section--text-overfilling):not(.section--text-produit){
      .section--text--content,
      .section--text__wrapper{
        h3{
          width: 100%;
          text-align: center;
        }
        p{
          @include media-breakpoint-down(sm){
            text-align: center;
          }
        }
      }
    }
    &:not(.section--big-title):not(.section--text-overfilling){
      .section--text--content{
        h3{
          @extend .title--h2-small;
        }
      }
    }
    &.section--text-produit{
      &.section--text-produit--shifted{
        .section--text--content{
          h3{
            @extend .title--h2-small;
          }
        }
      }
      &:not(.section--text-produit--shifted){
        .section--text--content{
          h3{
            // @extend .title--sep;
            // text-transform: uppercase;
            // padding-bottom: 0;
            // margin-bottom: 20px;
            // display: block;
            @include media-breakpoint-down(sm){
              margin-top: 20px;
            }
            &:after{
              display: none;
            }
          }
          a:not(.button){
            @extend .link;
            display: inline-block;
            margin-bottom: 20px;
          }
        }
      }
      &:not(.section--text-produit--shifted):not(.section--produit){
        .section--text--content{
          a:not(.button){
            border-bottom: 1px solid $color__secondary;
            padding-bottom: 10px;
            margin-bottom: 20px;
            @include media-breakpoint-down(sm){
              margin-bottom: 0;
            }
          }
        }
      }
    }
    &.section--text-produit-2{
      .row:nth-child(1){
        // @include media-breakpoint-down(sm){
        //   flex-direction: column-reverse;
        // }
      }
      .col-12:nth-child(1){
        .section--text--white{
          padding-left: 0;
          padding-right: 0;
        }
      }
      .col-12:nth-child(2){
        .section--text--white{
          padding-left: 0;
        }
      }
      .row:nth-child(1){
        position: relative;
        z-index: 2;
      }
      .row:nth-child(2){
        z-index: 1;
      }
    }
    &.section--text-produit-3{

    }
    .responsive-bg{
      position: absolute;
      top: 0;
      right: 15px;
      bottom: 0;
      width: calc(50vw - 23px );
      height: 100%;
      background-position: center;
      @extend %responsive--section-fullsize;
    }
    &.section--text-reverse{
      .section--text__wrapper{
        flex-direction: row-reverse;
        .responsive-bg{
          right: auto;
          left: 15px;
        }
      }
    }
    &.section--text-produit{
      .resultat-prouve__wrapper{
        @include media-breakpoint-down(sm){
          display: flex;
          .resultat-prouve{
            width: 50%;
            padding: 0 15px;
          }
        }
      }
      .resultat-prouve{
        .title{
          span{
            color: $color__primary;
          }
        }
      }
      .section--text--content{
        @include media-breakpoint-down(sm){
          text-align: center;
          .button{
            margin: 30px auto 0;
            max-width: 60%;
          }
        }
      }
    }
    .section--text__wrapper{
      @include media-breakpoint-down(sm){
        flex-direction: column-reverse !important;
      }
    }
  }
  &.section--text-produit--reverse{
    .row{
      flex-direction: row-reverse;
    }
  }
  &--routine-beaute--nuit{
    .card--product--routine-beaute{
      @extend .nuit;
    }
  }
  &--mag{
    .filters{
      @include media-breakpoint-down(sm){
        // flex-wrap: wrap;
        overflow: auto;
        justify-content: flex-start;
        margin-bottom: 20px !important;
      }
    }
    .mag__header{
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 20px;
      ul{
        li{
          display: flex;
          align-items: center;
          a,span{
            opacity: 1;
          }
        }
      }
      .mag__header__link{
        li{
          @include media-breakpoint-down(sm){
            .text--category{
              margin-bottom: 0;
            }
          }
          &.back{
            a{
              margin-right: 32px;
              @include media-breakpoint-down(sm){
                margin-right: 0;
              }
              .icon{
                font-size: 1rem;
                color: $color__secondary;
              }
              &:hover i.icon{
                color: $color__primary;
              }
            }
          }
          &.sep{
            padding: 0 5px;
          }
          &.seeMore{
            a{
              text-decoration: underline;
            }
          }
        }
      }
    }
    .mag__banner{
      position: relative;
      margin-bottom: 30px;
      @extend %responsive--visuel-fullsize;
      img{
        width: 100%;
      }
      h1{
        background-color: $color__white;
        padding: 0 68px 28px 60px;
        position: absolute;
        top: 0;
        left: 0;
        max-width: 600px;
        z-index: 2;
        @include media-breakpoint-down(sm){
          padding: 0 20px 20px 20px;  
          max-width: 85%;
        }
      }
      .list--rs{
        display: flex;
        justify-content: center;
        margin-top: 20px;
        li{
          padding: 0 10px;
          .icon{
            background-color: $color__primary;
            color: $color__white;
          }
        }
      }
    }
    .mag__subtitle{
      margin-bottom: 47px;
      @include media-breakpoint-down(sm){
        margin-bottom: 0;
      }
      p{
        color: $color__primary;
        font-family: $font__secondary;
        font-size: $fontSize__big-plus;
        line-height: $fontSize__big-plus + 1.9rem;
        letter-spacing: 2.86px;
        font-weight: $fontWeight__semi-bold;
        font-style: italic;
        text-align: center;
        @include media-breakpoint-down(sm){
          font-size: $fontSize__medium-plus;
          line-height: $fontSize__medium-plus + .8rem;
        }
      }
    }
    .mag__content{
      @extend .vertical-columns;
      @extend .columns-count-2;
      @extend .columns-gap;
      p{
        margin-bottom: 30px;
      }
      ul{
        @include media-breakpoint-down(sm){
          margin-bottom: 30px;
        }
      }
      p:first-child{
        @extend .text--intro;
        margin-bottom: 50px;
        line-height: 29px;
      }
      p:nth-child(2)::first-letter{
        font-weight: $fontWeight__semi-bold;
        color: $color__primary;
        font-size: 3.8rem;
        font-family: $font__secondary;
      }
      img{
        width: 100%;
        height: auto;
      }
    }
  }
  &--text-center{
    .section--text__visuel{
      margin-top: 40px;
    }
    @include media-breakpoint-down(sm){
      .section--text__wrapper{
        flex-direction: column !important;
      }
      .section--text__visuel{
        margin-top: 15px;
      }
    }
  }
  &--title-text-text2cols,
  &--text-overfilling{
    h3{
      @extend .title--sep;
    }
    .section--text__wrapper{
      min-height: 488px;
      @include media-breakpoint-down(sm){
        min-height: auto;
      }
      .responsive-bg{
        @include media-breakpoint-down(sm){
          padding-bottom: 100%;
          position: initial;
          width: calc( 100% + 30px );
          margin: 0 -15px;
        }
      }
    }
  }
  &--checkout{
    &.section--checkout-connexion{
      
      // .col-1{
      //   padding-right: 70px;
      //   border-right: 1px solid $color__primary;
      // }
      // .col-2{
      //   padding-left: 70px;
      // }
    }
    & .checkout--links{
      margin-top: 40px;
      @include media-breakpoint-down(sm){
        margin-top: 20px;
        margin-bottom: 25px;
        display: flex;
        justify-content: center;
        a{
          font-size: $fontSize__default--mobile;
          justify-content: center;
        }
      }
    }
    @include media-breakpoint-down(sm){
      #billing-address + label{
        padding-top: 10px;
      }
      form .form__footer{
        justify-content: start;
      }
      form{
        .button{
          max-width: 100%;
          width: 100%;
        }
      }
    }
  }
  &--checkout-les-plus{
    @include media-breakpoint-down(sm){
      padding-top: 40px;
      .banner{
        margin-top: 0 !important;
      }
    }
  }
  &--compte{
    form + form {
      margin-top: 30px;
      padding-top: 50px;
      border-top: 1px solid $color__primary;
    }
  }
  &--storelocator{
    @include media-breakpoint-down(md){
      margin-top: 0;
    }
  }
  &--404{
    @include media-breakpoint-down(sm){
      .text--highlight{
        margin-top: 10px;
      }
      h1,p,.text--highlight{
        display: block;
        text-align: center;
      }
      .button{
        margin: 0 auto 20px;
      }
    }
  }
  &--product-soin-comment-ca-marche{
    .title--border{
      h2{
        @include media-breakpoint-down(sm){
          font-family: $font__main;
          text-transform: uppercase;
          letter-spacing: 0.93px;
          display: block;
          margin: 0 !important;
          border-bottom: 1px solid $color__grey--extra-light;
          padding: 14px 0;
          display: flex;
          justify-content: space-between;
          font-size: $fontSize__default;
          line-height: $fontSize__default + .9rem;
          color: $color__black;
          opacity: 1;
          &:before{
            display: none;
          }
          &:after{
            content: "+" !important;
            padding: 0 10px !important;
            display: block !important;
            font-weight: $fontWeight__semi-bold !important;
            font-size: $fontSize__small !important;
            height: auto;
            width: auto;
            position: inherit;
            background-color: transparent;
            left: 0;
            color: $color__primary;
          }
          &.active{
            margin-bottom: 10px !important;
            &:after{
              content: "-" !important;
            }
          }
        }
      }
    }
    .etapes__wrapper{
      @include media-breakpoint-down(sm){
        padding-top: 20px;
        display: none;
        > *:last-child{
          border-bottom: 1px solid $color__grey--extra-light;
          padding-bottom: 20px;
        }
        ul{
          li{
            h3{
              font-size: $fontSize__default;
              line-height: $fontSize__default + .9rem;
            }
          }
        }
      }
    }
    ul{
      @extend %list--reset;
      li{
        text-align: center;
        h3{
          display: block;
        }
        span{
          &:before{
            content: "-";
            padding-right: 5px;
          }
        }
        & + li{
          margin-top: 25px;
        }
      }
    }
    .part--reassurance-vente{
      margin-top: 76px;
      @include media-breakpoint-down(sm){
        margin-top: 40px;
      }
    }
  }
  .part--reassurance-vente{
    @include media-breakpoint-down(sm){
      margin-top: 20px;
    }
  }
}