.pagination{
    margin: 0;
    justify-content: center;
    .page-item{
        .page-link{
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            border: 0;
            color: $color__secondary;
            font-size: $fontSize__default;
            padding: 5px 10px;
            span{
                font-size: 9px;
                align-items: center;
                display: flex;
                i{
                    margin-top: 2px;
                    font-size: $fontSize__breadcrumb;
                    color: $color__text;
                }
            }
        }
        &.active{
            .page-link{
                background-color: inherit;
                font-weight: $fontWeight__bold;
            }
        }
        &.disabled{
            .page-link{
                opacity: 0.5;
            }
        }
    }
}