$loader__icon-size : 60px;
$loader__icon-size--big : 100px;
.loader{
    display: flex;
    justify-content: center;
    position: relative;
    margin: 20px 0;    
    height: $loader__icon-size;
    .icon{
        display: block;
        font-size: $loader__icon-size;
        width: $loader__icon-size;
        height: $loader__icon-size;
        line-height: $loader__icon-size;
        text-align: center;
        color: $color__secondary;
    }
    .logo-filet{
        .icon{
            color: $color__white;
            text-shadow: 0 0 3px $color__secondary;
        }
    }
    .logo{
        position: absolute;
        height: 0%;
        width: 100%;
        overflow: hidden;
        bottom: 0;
        display: flex;
        justify-content: center;
        animation: loader 2s ease-in-out infinite;
        .icon{
            position: absolute;
            bottom: 0;
            height: $loader__icon-size;
        }
    }
    &.loader--big{
        height: $loader__icon-size--big;
        .icon{
            font-size: $loader__icon-size--big;
            width: $loader__icon-size--big;
            height: $loader__icon-size--big;
            line-height: $loader__icon-size--big;
        }
    }
    &.loader--primary{
        .logo .icon{
            color: $color__primary;
        }
    }
}
@keyframes loader{
    0%{
        height: 0%;
        opacity: 1;
    }
    80%{
        height: 100%;
        opacity: 1;
    }
    100%{
        opacity: 0;
        height: 100%;
    }
}