%list--reset{
  list-style: none;
  margin: 0;
  padding: 0;
}

ul{
  &.list--reset{
    @extend %list--reset;
  }
  &.list--horizontal{
    @extend .list--reset;
    display: flex;
  }
  &.features{
    @extend %list--reset;
    border-top: 1px solid $color__primary;
    li{
      border-bottom: 1px solid $color__grey--extra-light;
      padding: 14px 0;
      display: flex;
      align-items: center;
      span{
        opacity: 0.6;
        @include media-breakpoint-down(sm){
          opacity: 1;
        }
      }
      strong{
        @extend .title--highlight;
        font-weight: $fontWeight__default;
        color: $color__secondary;
        width: 35%;
        min-width: 35%;
        opacity: 1;
        @include media-breakpoint-down(sm){
          font-weight: $fontWeight__bold;
        }
      }
    }
  }
  &.list--light{
    @extend %list--reset;
    li{
      &:before{
        content: "-";
        padding-right: 5px;
      }
    }
  }
  &.list--rs{
    li{
      padding: 0 4px;
    }
  }
  &.list--ingredients{
    @extend %list--reset;
    li{
      display: inline-block;
      text-transform: uppercase;
      letter-spacing: 0.93px;
      & + li {
        &:before{
          content: "|";
          padding: 0 10px;
        }
      }
    }
  }
}