.filters{
    @include media-breakpoint-down(sm){
        display: none;
    }
    .title{
        @extend .text--highlight;
        font-weight: $fontWeight__semi-bold;
        color: $color__secondary;
        opacity: 1;
        padding-left: 10px;
        padding-bottom: 14px;
        @include media-breakpoint-down(sm){
            display: none;
        }
    }
    ul{
        @extend %list--reset;
    }
    > ul:not(.filters__remove):not(.filters__submenu){
        > li{
            border-bottom: 1px solid rgba($color__black, .2);
            &:first-child{
                border-top: 1px solid rgba($color__black, .2);
            }
            > a{
                @include filter();
                @include media-breakpoint-down(sm){
                    padding: 20px;
                    margin-bottom: 0;
                    font-weight: $fontWeight__bold;
                    font-size: $fontSize__extra-small;
                }
            }
            ul.filters__submenu{
                display: none;
            }
        }
    }
    ul.filters__submenu{
        padding-bottom: 15px;
        li{
            a{
                color: $color__black;
                opacity: 0.6;
                padding: 5px 10px 5px 28px;
                display: block;
                @include media-breakpoint-down(sm){
                    padding-top: 7px;
                    padding-bottom: 7px;
                }
                &:hover{
                    opacity: 1;
                    transform: translateX(5px);
                    color: $color__primary;
                }
                &.actif{
                    opacity: 1;
                    color: $color__primary;
                    font-weight: $fontWeight__bold;
                }
            }
        }
        &--categories{
            border-top: 1px solid rgba($color__black, .2);
            padding-top: 10px;
            padding-bottom: 80px;
            @include media-breakpoint-down(sm){
                padding-bottom: 20px;
            }
        }
    }
    .filters__remove{
        border-top: 1px solid rgba($color__black, .2);
        padding: 10px 0;
        @include media-breakpoint-down(sm){
            border-top: 0;
        }
        li{
            a{
                padding: 5px 10px 5px 8px;
                color: $color__text;
                display: flex;
                align-items: center;
                @include media-breakpoint-down(sm){
                    padding: 5px 10px;
                }
                .icon{
                    width: 20px;
                    color: $color__text;
                    font-weight: 700;
                    color: $color__primary;
                    font-size: .8rem;
                    &:before{
                        margin-bottom: -1px;
                    }
                }
                &:hover{
                    color: $color__primary;
                }
            }
        }
    }
    &--horizontal{
        @extend .list--horizontal;
        justify-content: center;
        margin-bottom: 10px !important;
        flex-wrap: wrap;
        li{
            padding: 0 10px 18px;
            a{
                white-space: nowrap;
                &.actif{
                    @extend .button--primary-fill;
                }
                @include media-breakpoint-down(md){
                    &.button{
                        padding: 5px 10px;
                        min-height: 40px;
                    }
                }
            }
        }
    }
    .link--checkbox{
        padding: 10px 10px 10px 28px;
        justify-content: space-between;
        @include media-breakpoint-down(sm){
            padding: 20px;
            margin-bottom: 0;
            font-weight: $fontWeight__bold;
            font-size: $fontSize__extra-small;
        }
        & + .link--checkbox{
            padding-top: 0;
        }
    }
}
.filters-button-mobile{
    // margin-bottom: 20px;
    border-left: 0;
    border-right: 0;
    border-top: 0;
    padding: 15px;
    font-weight: $fontWeight__bold;
    width: 100%;
    max-width: 100%;
    &:after{
        content: "+";
        padding-left: 5px;
    }
    &.active{
        &:after{
            content: "-";
        }
    }
}