@mixin dot($bottom: -10px, $size: 6px) {
    position: relative;
    &:after{
        content: "";
        display: block;
        width: $size;
        height: $size;
        background-color: $color__primary;
        border-radius: 50%;
        position: absolute;
        bottom: $bottom;
        left: 50%;
        transform: translateX(-50%);
      }
}

@mixin filter(){
  @extend .text--highlight;
  color: $color__secondary;
  opacity: 1;
  padding: 10px 10px 10px 28px;
  font-weight: $fontWeight__default;
  display: flex;
  justify-content: space-between;
  position: relative;
  transition: all 250ms ease;
  &:hover{
      color: $color__primary !important;
  }
  &:after{
      content: "+";
      font-weight: $fontWeight__bold;
      font-size: $fontSize__small;
      display: block;
  }
  &.open{
      // color: $color__primary;
      &:after{
          content: "-";
      }
  }
}