address{
    text-align: center;
    font-size: $fontSize__small-plus;
    line-height: $fontSize__small-plus + 1.3rem;
    margin-top: 66px;
    margin-bottom: 66px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    @include media-breakpoint-down(sm){
        margin: 0 0 40px;
    }
    a{
        color: $color__secondary;
    }
    .button{
        margin: 66px auto 0;
        @include media-breakpoint-down(sm){
            margin-top: 40px;
        }
    }
}