nav.navbar{
  padding: 0;
  position: inherit;
  .navbar__wrapper{
    width: 100%;
    position: inherit;
    .navbar__collapse{
      .navbar__menu{ 
        @extend %list--reset;
        display: flex;
        justify-content: space-between;
        overflow: auto;
        max-height: calc( 100vh - #{$header__height-mobile} );
        @include media-breakpoint-down(md){
          flex-direction: column;
        }
        &.navbar__menu--desktop{
          display: flex;
          @include media-breakpoint-down(md){
            display: none;
          }
        }
        &.navbar__menu--mobile{
          display: none;
          @include media-breakpoint-down(md){
            display: flex;
          }
        }
        .menu-item{
          flex-grow: 1;
          // position: relative;
          > a:not(.mobile--all){
            text-align: center;
            color: $color__secondary;
            font-weight: $fontWeight__bold;
            letter-spacing: 1px;
            text-transform: uppercase;
            display: block;
            padding: 10px 5px;
            font-size: $fontSize__extra-small;
            border-bottom: 1px solid $color__grey;
            &.e-shop{
              background-color: $color__primary;
              color: $color__white;
              &:hover{
                background-color: $color__white;
                color: $color__primary;
              }
            }
            @include media-breakpoint-down(md){
              text-align: left;
              padding: 15px;
              // margin-top: 20px;
              border-color: $color__grey--extra-light2;
            }
            @include media-breakpoint-up(lg){
              color: $color__white;
              border: 0;
            }
            @include media-breakpoint-up(xl){
              padding: 10px 15px;
            }
          }
          &.menu-item--large{
            margin: 0 -20px;
            border-top: 1px solid $color__secondary;
            a{
              padding: 15px 35px;
              border-bottom: 0;
            }
          }
          &.menu-item--gris{
            margin-top: 10px;
            a{
              color: $color__grey--dark;
              font-weight: $fontWeight__default;
              border-bottom: 0;
            }
            & + .menu-item--gris{
              margin-top: 0;
            }
            & + .menu-item--large{
              margin-top: 10px;
            }
          }
          &.menu-item--color{
            a{
              background-color: $color__secondary;
              color: $color__white;
            }
          }
          &.menu-item--has-child{
            @include media-breakpoint-down(md){
              >a{
                display: flex;
                width: 100%;
                justify-content: space-between;
                &:after{
                  content :"+";
                  color: $color__primary;
                  font-size: $fontSize__medium;
                }
                &.active{
                  &:after{
                    content: "-";
                  }
                }
              }
              .container> .row > .col-12 + .col-12{
                .menu-item__title{
                  margin-top: 30px;
                }
              }
            }
          }
          a.mobile--all{
            padding-top: 17px;
            padding-bottom: 2px;
            display: block;
            font-size: $fontSize__extra-small;
            text-transform: uppercase;
            color: $color__grey;
            @include media-breakpoint-down(md){
              text-transform: none;
              color: $color__primary;
              text-decoration: underline;
              font-weight: $fontWeight__bold;
            }
          }
          &.current-menu-item,
          &:hover{
            >a{
              background-color: $color__white;
              color: $color__primary;
            }
          }
          .menu-item__submenu{
            @include media-breakpoint-down(md){
              display: none;
              border-bottom: 1px solid $color__grey--extra-light2;
              padding-bottom: 20px;
            }
            @include media-breakpoint-up(lg){
              box-shadow: $shadow-base;
              display: none;
              background-color: $color__white;
              position: absolute;
              top: 100%;
              left: 0;
              width: 100%;
              padding: 30px 20px 40px;
              z-index: 20;
              list-style: none;
            }
            @include media-breakpoint-down(md){
              > .container{
                max-width: 100%;
              }
            }
            .menu-item__title{
              font-weight: $fontWeight__bold;
              text-transform: uppercase;
              letter-spacing: 1px;
              margin-bottom: 12px;
              display: block;
              @include media-breakpoint-down(md){
                margin: 10px 0 5px;
                font-size: $fontSize__extra-small;
                &.title--border{
                  text-align: left;
                  strong{
                    padding: 0;
                    margin: 0;
                    &:before, &:after{display: none;}
                  }
                }
              }
              &--separator{
                @include media-breakpoint-up(lg){
                  margin-top: 70px;
                }
              }
            }
            p{
              line-height: $fontSize__default + 1.2rem;
            }
            ul{
              @extend %list--reset;
              .menu-item__submenu-item{
                line-height: $fontSize__default + .8rem;
                a{
                  color: $color__secondary;
                  border: 0;
                  font-size: $fontSize__extra-small;
                  text-transform: uppercase;
                  color: $color__grey;
                  font-weight: $fontWeight__default;
                  display: block;

                  @include media-breakpoint-down(md){
                    padding: 7px 0;
                    text-transform: none;
                    color: $color__secondary;
                    .menu-item__title{
                      margin: 0 !important;
                    }
                  }
                  @include media-breakpoint-up(md){
                    color: $color__secondary;
                    font-size: $fontSize__default;
                    text-transform: none;
                    letter-spacing: 1px;
                    &:hover{
                      color: $color__primary;
                      transform: translate(2px);
                    }
                  }
                }
              }
              & + .menu-item__title{
                margin-top: 30px;
                @include media-breakpoint-down(md){
                  margin-top: 10px;
                }
              }
            }
            .menu-item__submenu--large{
              li{
                @include media-breakpoint-up(lg){
                  line-height: $fontSize__default + 1.9rem;
                }
              }
            }
            .row.sep{
              @include media-breakpoint-up(lg){
                margin-top: 40px;
              }
            }
          }
          &:hover{
            .menu-item__submenu{
              @include media-breakpoint-up(lg){
                display: block;
              }
            }
          }
        }
      }
      position: relative;
      .e-shop--overlay{
        position: absolute;
        right: 0;
        top: calc( 100% + 15px);
        width: 326px;
        background-color: $color__white;
        padding: 28px 35px;
        text-align: center;
        border: 1px solid $color__secondary;
        transform: translateX( calc(50% - 70px) );
        display: none;
        font-size: $fontSize__small;
        &.show{
          display: block;
        }
        .button{
          & + .button{
            margin-top: 13px;
          }
        }
        ul{
          padding: 0 10px;
          margin-bottom: 20px;
        }
        &:before, &:after{
          content: "";
          left: 50%;
          position: absolute;
          top: -6px;
          transform: translateX(-50%);
          z-index: 2;
        }
        &:before{
          width: 0;
          height: 0;
          border-style: solid;
          border-width: 0 10px 6px 10px;
          border-color: transparent transparent $color__secondary transparent;
        }
        &:after{
          width: 0;
          height: 0;
          border-style: solid;
          border-width: 0 8px 5px 8px;
          border-color: transparent transparent $color__white transparent;
          top: -5px;
        }
      }
    }
  }
}