.button{
   padding: 7px 24px;
   letter-spacing: 1px; 
    color: $color__secondary;
    border: 1px solid $color__secondary;
    overflow: hidden;
    position: relative;
    display: flex;
    max-width: fit-content;
    text-transform: uppercase;
    font-size: $fontSize__extra-small;
    cursor: pointer;
    z-index: 1;
    align-items: center;
    small{
        font-size: $fontSize__extra-small;
        color: $color__primary;
        font-weight: $fontWeight__bold;
        transition: all 250ms ease-in-out;
        padding-left: 4px;
    }
    // background-color: $color__white;
    @include media-breakpoint-down(sm){
        padding: 10px 15px;
        // width: 100%;
        // max-width: 100%;
        justify-content: center;
        text-align: center;
    }
    @media all and (-ms-high-contrast: none), (-ms-high-contrast: active){
        float: left;
        clear: both;
    }
    &:before{
        content: "";
        position: absolute;
        width: 0px;
        height: 0px;
        bottom: 0px;
        left: 50%;
        transform: translateX(-50%) translateY(50%);
        background-color: $color__secondary;
        transition: all 500ms ease;
        border-radius: 50%;
        z-index: -1;
    }
    &:hover{
        color: $color__white;
        &:before{
            width: 200%;
            height: 300%;
        }
    }
    &--submit{
        padding: 0;
        input{
            padding: 7px 24px;
            background-color: transparent;
            border: 0;
            margin: 0;
            transition: all 250ms ease;
            text-transform: uppercase;
            @include media-breakpoint-down(sm){
                line-height: 24px;
                padding: 10px 15px;
            }
            &:hover{
                color: $color__white;
            }
        }
    }
    &--full-size{
        width: 100%;
        max-width: 100%;
        justify-content: center;
    }
    &--small{
        padding: 7px 10px;
    }
    &--white{
        color: $color__white;
        border-color: $color__white;
        &:before{
            background-color: $color__white;
        }
        &:hover{
            color: $color__secondary;
        }
    }
    &--primary{
        color: $color__primary;
        border-color: $color__primary;
        background-color: $color__white;
        &:before{
            background-color: $color__primary;
        }
        &:hover{
            color: $color__secondary;
        }
    }
    &--primary-fill{
        color: $color__white;
        border-color: $color__primary;
        background-color: $color__primary;
        input{
            color: $color__white;
        }
        small{
            color: $color__white;
        }
        &:before{
            background-color: $color__white;
        }
        &:hover, input:hover, &:hover small{
            color: $color__primary;
        }
    }
    &--secondary-fill{
        color: $color__white;
        border-color: $color__secondary;
        background-color: $color__secondary;
        input{
            color: $color__white;
        }
        &:before{
            background-color: $color__white;
        }
        &:hover, input:hover{
            color: $color__secondary;
        }
    }
    &--dark{
        color: $color__white;
        border-color: $color__secondary;
        background-color: $color__secondary;
        &:before{
            background-color: $color__white;
        }
        &:hover{
            color: $color__secondary;
        }
    }
    &--dark-hover{
        &:before{
            background-color: $color__secondary;
        }
    }
    &--close{
        padding: 0;
        border: 0;
        background-color: transparent;
        .icon{
            font-size: $header__height-mobile;
            width: $header__height-mobile;
            height: $header__height-mobile;
            display: flex;
            align-items: center;
            justify-content: center;
            color: $color__secondary;
        }
        &:hover{
            .icon{
                color: $color__primary;
            }
        }
    }
    &--center{
        margin: 0 auto;
    }
    &[disabled], & input[disabled]{
        background-color: $color__grey !important;
        border-color: $color__grey !important;
        color: $color__white !important;
        cursor: not-allowed;
    }
}