#storelocator{
    .leaflet-control-attribution,
    .leaflet-control-container{
        .leaflet-bottom{
            display: none;
        }
    }
    .loading-container{
        display: flex;
        .list-container,
        .map-container{
            height: 100%;
            position: relative;
        }
        .list-container{
            z-index: 2;
            width: 33.333333%;
            // padding-right: 20px;
            overflow-y: hidden;
            box-shadow: 0 0px 23px 0 rgba(0,0,0,.3);
            @include media-breakpoint-down(md){
                width: 100%;
                position: absolute;
                background-color: $color__white;
                transform: translateX(-100%);
                transition: all 500ms ease;
                padding-right: 0;
                &.show{
                    transform: translateX(0%);
                }
            }
            form{
                height: 100%;
                max-height: 100%;
                display: flex;
                flex-direction: column;
                .dynamic-list-container{
                    overflow-y: auto;
                }
                .fixed-list-container{
                    .storelocator__item{
                        background-color: $color__secondary;
                        &:before{
                            z-index: 1;
                        }
                        &:hover:before{
                            display: none;
                        }
                        *{
                            color: $color__white;
                        }
                    }
                }
            }
            &.disabled-overflow{
                overflow: hidden;
            }
            .no-result{
                text-align: center;
                padding: 40px 10px;
                display: block;
            }
        }
        .map-container{
            z-index: 1;
            width: 66.6666666%;
            @include media-breakpoint-down(md){
                width: 100%;
            }
            #mapid{
                height: 100%;
                width: 100%;
            }
        }
    }
    &__filter{
        #filters{
            @include media-breakpoint-down(md){
                position: fixed;
                top: 50%;
                left: 50%;
                width: calc( 100% - 40px );
                transform: translate(-50%, -50%);
                background-color: $color__white;
                padding: 25px;
                padding-top: 30px;
                border-radius: 5px;
                // height: 100%;
                z-index: 203;
                display: none;
                h2{
                    text-align: center;
                    margin-bottom: 20px;
                }
                .close{
                    position: absolute;
                    top: 10px;
                    right: 5px;
                    font-size: 3rem;
                    color: $color__secondary;
                }
                .button.button--primary-fill{
                    margin: 0 auto;
                }
            }
            .filters__where{
                position: relative;
                input{
                    padding-right: 171px;
                    @include media-breakpoint-down(md){
                        padding-right: 10px;
                    }
                }
                #around-me{
                    display: flex;
                    position: absolute;
                    bottom: 3px;
                    right: 3px;
                    background-color: $color__primary;
                    color: $color__white;
                    align-items: center;
                    letter-spacing: 0.76px;
                    text-transform: uppercase;
                    width: 166px;
                    height: 34px;
                    @include media-breakpoint-down(md){
                        position: relative;
                        margin: 0 auto;
                        margin-top: 15px;
                        background-color: $color__secondary;
                        height: 44px;
                    }
                    i{
                        color: $color__white;
                    }
                }
            }
            .filters__where{
                position: relative;
                .location-suggest-results{
                    @extend .list--reset;
                    position: absolute;
                    z-index: 10;
                    background-color: $color__white;
                    box-shadow: 0px 10px 10px 0 rgba(0,0,0,.3);
                    width: 100%;
                    li{
                        padding: 10px;
                        &:hover{
                            background-color: $color__grey--extra-light;
                            cursor: pointer;
                        }
                    }
                }
            }
        }
        .filters__checkbox--wrapper{
            display: flex;
            .filters__checkbox{
                margin-top: 33px;
                @include media-breakpoint-down(md){
                    // margin-top: 0;
                }
                & + .filters__checkbox{
                    margin-left: 20px;
                }
            }
        }
        .filter-button{
            @extend .button--primary-fill;
            position: fixed;
            z-index: 19;
            bottom: 20px;
            left: 50%;
            transform: translateX(-50%);
            box-shadow: 0px 0px 10px 0 rgba(0,0,0,.3);
            white-space: nowrap;
            max-width: max-content;
        }
        .bg-filter{
            background-color: rgba(0,0,0,.8);
            position: fixed;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            width: 100%;
            height: 100%;
            z-index: 202;
            display: none;
        }
        .mobilefilter{
            
        }
    }
    &__map{
        box-shadow: 0 0px 13px 0 rgba(0,0,0,.3);
        overflow: hidden;
        position: relative;
        z-index: 1;
        margin-top: 10px;
        @include media-breakpoint-down(md){
            box-shadow: 0 0px 0px 0 rgba(0,0,0,0);
            margin-top: 0;
        }
        .storelocator__mobile-tabs{
            display: none;
            position: relative;
            z-index: 20;
            a{
                @extend .button;
                display: flex;
                width: 50%;
                min-width: 50%;
                justify-content: center;
                border-left: 0;
                border-right: 0;
                border-top-color: $color__grey--extra-light2;
                background-color: $color__white;
                &.active{
                    @extend .button:hover;
                }
            }
            @include media-breakpoint-down(md){
                display: flex;
            }
        }
        .loading-container{
            height: 740px;
            @include media-breakpoint-down(md){
                max-height: calc( 100vh - 175px );
            }
            #mapid {
                height: 100%;
            }
        }
        .storelocator__item{
            display: flex;
            padding: 15px 15px 15px 20px;
            border-bottom: 1px solid $color__grey--extra-light2;
            position: relative;
            @include media-breakpoint-down(md){
                img{
                    max-width: 90px;
                    height: auto;
                }
            }
            &:before{
                content:"";
                height: calc(100% - 30px);
                position: absolute;
                left: 0;
                top: 50%;
                transform: translateY(-50%);
                background-color: $color__grey--light;
                width: 5px;
                z-index: -1;
                transition: width 200ms ease, height 200ms ease;
            }
            
            &.color--institut{
                &:before{ background-color: $color__institut; }  
                i{ color: $color__institut; }
            }
            &.color--day-spa{
                &:before{ background-color: $color__day-spa; }
                i{ color: $color__day-spa; }
            }
            &.color--spa-hotelier{
                &:before{ background-color: $color__spa-hotelier; }
                i{ color: $color__spa-hotelier; }
            }
            .storelocator__item-img{
                position: relative;
                display: block;
                .tag{
                    position: absolute;
                    right: 0;
                    top: 0;
                    @include media-breakpoint-down(xs){
                        top: -8px;
                        transform: scale(0.9);
                    }
                }
                .spas-by-sothys{
                    background-color: $color__white;
                    display: flex;
                    width: 26px;
                    height: 26px;
                    justify-content: center;
                    align-items: center;
                    position: absolute;
                    top: 0;
                    right: -13px;
                    @include media-breakpoint-down(xs){
                        top: -8px;
                        transform: scale(0.9);
                    }
                    i{
                        color: $color__primary !important;
                    }
                    & + .tag{
                        right: 13px;
                        @include media-breakpoint-down(xs){
                            right: 9px;
                        }
                    }
                }
                img{
                    display: block;
                }
            }
            .storelocator__item-content{
                padding-left: 20px;
                display: flex;
                flex-direction: column;
                justify-content: center;
                .title{
                    font-weight: $fontWeight__semi-bold;
                    font-size: $fontSize__medium;
                    font-family: $font__secondary;
                    color: $color__secondary;
                    @include media-breakpoint-down(md){
                        font-size: $fontSize__small-plus;
                        line-height: $fontSize__small-plus + .2rem;
                    }
                }
                .city{
                    padding-top: 5px;
                    font-size: $fontSize__extra-extra-small;
                    letter-spacing: 0.64px;
                    color: rgba($color__black,.6);
                    text-transform: uppercase;
                    @include media-breakpoint-down(md){
                        font-size: 1rem;
                    }
                }
                .icon{
                    position: absolute;
                    right: 0;
                    top: 50%;
                    transform: rotate(-90deg);
                    margin-top: -15px;
                    font-size: 12px;
                    font-weight: 700;
                    @include media-breakpoint-down(md){
                        display: none;
                    }
                }
            }
            &:hover{
                &:before{
                    width: 100%;
                    height: 100%;
                }
                .storelocator__item-content{
                    .title,
                    .city,
                    .icon{
                        color: $color__white;
                    }
                    .icon{
                        padding-bottom: 10px;
                    }
                }
            }
            &--popup{
                min-width: 250px;
                padding: 0;
                border: 0;
                .storelocator__item-content{
                    .title{
                        font-size: $fontSize__small;
                    }
                }
                &:hover{
                    .title,.city{color: $color__secondary !important;}
                }
            }
        }
        .storelocator__item-info{
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            z-index: 2;
            background-color: $color__white;
            transform: translateX(-100%);
            transition: all 250ms ease;
            @include media-breakpoint-down(md){
                height: calc( 100% - 40px );
            }
            .close{
                background-color: $color__grey--light;
                display: flex;
                height: 52px;
                align-items: center;
                padding: 0 10px;
                color: $color__white;
                text-transform: uppercase;
                .icon{
                    color: $color__white;
                    transform: rotate(90deg);
                    // margin-bottom: -15px;
                    font-size: 10px;
                    font-weight: 700;
                    margin-right: 10px;
                }
            }
            &.color--institut{
                .close{ background-color: $color__institut; }  
            }
            &.color--day-spa{
                .close{ background-color: $color__day-spa; }
            }
            &.color--spa-hotelier{
                .close{ background-color: $color__spa-hotelier; }
            }
            &-content{
                padding: 30px;
                overflow-y: auto;
                max-height: calc( 100% - 52px );
                .info-section{
                    margin-bottom: 50px;
                }
                .city{
                    padding-top: 5px;
                    font-size: $fontSize__extra-extra-small;
                    letter-spacing: 0.64px;
                    color: $color__black;
                    text-transform: uppercase;
                    display: block;
                    @include media-breakpoint-down(md){
                        opacity: 0.5;
                        font-size: 1rem;
                        margin-bottom: 10px;
                    }
                }
                .title{
                    // font-weight: $fontWeight__bold;
                    @extend .title--h2-medium;
                    // font-size: $fontSize__medium;
                    // font-family: $font__secondary;
                    // color: $color__secondary;
                    // display: block;
                    // margin-top: 5px;
                    margin-bottom: 26px !important;
                    @include media-breakpoint-down(md){
                        font-size: $fontSize__big !important;
                        line-height: $fontSize__big + .5rem;
                    }
                }
                p{
                    letter-spacing: 0.93px;
                }
                .title--sep{
                    margin-bottom: 30px;
                    padding-bottom: 10px;
                    display: block;
                    &:after{
                        background-color: $color__primary;
                        @include media-breakpoint-down(md){
                            // left: 50%;
                            // transform: translateX(-50%);
                        }
                    }
                }
                address{
                    align-items: flex-start;
                    margin: 0;
                    
                    span,a{
                        display: block;
                        font-size: $fontSize__default;
                        letter-spacing: 0.93px;
                        margin-bottom: 20px;
                        @include media-breakpoint-down(md){
                            text-align: left;
                        }
                    }
                    .website{
                        font-weight: $fontWeight__bold;
                    }
                }
                .button{
                    & + .button{
                        margin-top: 15px;
                    }
                }
                .fiche-spa{
                    display: flex;
                    justify-content: flex-end;
                    align-items: center;
                    color: $color__grey;
                    text-transform: uppercase;
                    i{
                        color: $color__grey;
                        font-size: 1.4rem;
                        margin-left: 10px;
                    }
                }
            }
            &.show{
                transform: translateX(0%);
            }
        }
        .loading-screen{
            position: absolute;
            z-index: 20;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            background-color: rgba($color__white, 0.9);
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: $fontSize__large;
        }
    }
    &.storelocator--filtersInline{
        @include media-breakpoint-down(md){
            .filter-button{
                position: relative;
                transform: initial;
                box-shadow: 0 0 0 0 #fff;
                left: 0;
                bottom: 0;
                margin: 0 auto;
                margin-bottom: 20px;
            }
        }
        @include media-breakpoint-down(sm){
            .filter-button{
                width: calc(100% + 30px);
                margin: 0 -15px -1px;
                max-width: unset;
            }
        }
    }
    
}