.modal{
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
    display: flex;
    justify-content: center;
    align-items: center;
    &__overlay,
    &__loader{
        position: fixed;
        left: 0;
        right: 0;
        bottom: 0;
        top: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
        transition: all 500ms ease 250ms;
        z-index: 10001;
    }
    &__overlay{
        background-color: rgba($color__black, .7);
    }
    &__loader{
        display: flex;
        justify-content: center;
        align-items: center;
        transition-delay: 500ms;
    }
    &__content{
        // position: absolute;
        transform: scale(0.7);
        background-color: $color__white;
        width: 785px;
        // height: 880px;
        opacity: 0;
        transition: all 500ms ease 250ms;
        z-index: 10001;
        border-radius: 10px;
        text-align: center;
        box-sizing: border-box;
        max-height: 90vh;
        overflow: hidden;
        .modal__wrapper{
            padding: 70px;
            background-color: $color__white;
            height: 100%;
            overflow-y: auto;
            max-height: 90vh;
            .modal__inner{
                h2{
                    color: $color__primary;
                    margin-bottom: 60px;
                }
                ul, p{
                    margin-bottom: 60px;
                }
                .button{
                    margin: 0 auto;
                }
            }
            .modal__footer{
                display: flex;
                justify-content: center;
                .button{
                    margin: 0 10px;
                }
            }
        }
        .modal__close{
            position: absolute;
            top: 10px;
            right: 10px;
            cursor: pointer;
            i{
                color: $color__secondary;
                font-size: 3rem;
            }
        }
    }
    &.show{
        z-index: 10000;
        .modal__overlay,
        .modal__loader{
            opacity: 1;
        }
    }
    &.loaded{
        .modal__loader{
            opacity: 0;
            z-index: -1;
        }
        .modal__content{
            opacity: 1;
            transform: scale(1);
        }
    }
    &.hide{
        .modal__overlay{
            opacity: 0;
            transition-delay: 500ms;
        }
        .modal__content{
            opacity: 0;
            transform: scale(1) translateY(150px);
        }
    }
    &.youtube{
        .modal__close{
            top: -25px;
            right: -25px;
            @include media-breakpoint-down(sm){
                right: 0;
                top: -30px;
            }
            i{
                color: $color__white;
                font-size: 4rem;
            }
        }
        .modal__content{
            border-radius: 0;
            width: 854px;
            height: 480px;
            overflow: visible;
            @include media-breakpoint-down(md){
                width: 640px;
                height: 360px;
            }
            @include media-breakpoint-down(sm){
                width: 100%;
            }
            .modal__wrapper{
                overflow: hidden;
                width: 854px;
                height: 480px;
                padding: 0;
                @include media-breakpoint-down(md){
                    width: 640px;
                    height: 360px;
                    iframe{
                        width: 640px;
                        height: 360px;
                    }
                }
                @include media-breakpoint-down(sm){
                    width: 100%;
                    iframe{
                        width: 100%;
                    }
                }
            }
        }
    }
}