$cart__width : 520px;
#cart{
    position: fixed;
    top: 0;
    right: 0;
    background-color: $color__white;
    z-index: 30;
    width: $cart__width;
    padding: 34px 48px;
    box-shadow: 0 2px 14px 0 rgba(0,0,0,.22);
    transform: translateX(100%);
    transition: transform 500ms ease;
    max-height: 100vh;
    overflow-y: auto;
    @include media-breakpoint-down(md){
        width: 100%;
        padding: 20px;
    }
    .cart__close-wrapper{
        display: flex;
        justify-content: flex-end;
        padding-bottom: 20px;
        .cart__close{
            @extend .button--close;
            float: right;
        }
    }
    .cart__title{
        @extend h2;
        // font-size: $fontSize__big-plus;
        // font-weight: $fontWeight__default;
        small{
            font-size: 60%;
        }
    }
    
}
body.cart--opened{
    #cart{
        transform: translateX(0);
    }
}

.cart--table{
    @include media-breakpoint-down(sm){
        margin-bottom: 20px;
    }
    thead{
        border-bottom: 1px solid $color__grey--dark;
        th{
            padding: 5px;
            text-transform: uppercase;
            letter-spacing: 0.93px;
            @include media-breakpoint-down(sm){
                &.product-quantity,
                &.product-price,
                &.product-remove{
                    display: none;
                }
            }
        }
    }
    td{
        padding: 14px 5px;
    }
    .product-thumbnail{
        // max-width: 60px;
        width: 90px;
        max-width: 90px;
        a{display: block;}
    }
    .product-quantity,
    .product-name{
        .text--category{
            // font-size: 1rem;
            // letter-spacing: 0.7px;
            opacity: 1;
        }
        a{
            display: block;
            font-family: $font__secondary;
            color: $color__secondary;
            font-size: $fontSize__small-plus;
            font-weight: $fontWeight__bold;
            @include media-breakpoint-down(sm){
                font-size: $fontSize__small;
                line-height: $fontSize__small + .2rem;
                margin-bottom: 10px;
            }
        }
    }
    .product-price{
        .price{
            font-weight: $fontWeight__semi-bold;
            font-size: $fontSize__small;
            white-space: nowrap;
        }
    }
    .product-remove{
        width: 30px;
        padding-right: 0;
        @include media-breakpoint-down(sm){
            width: auto;
        }
        a{
            display: flex;
            justify-content: flex-end;
            .icon{
                color: $color__secondary;
                justify-content: flex-end;
            }
        }
    }
    &-small{
        .product-name{
            a{
                margin-bottom: 10px;
            }
        }
    }
    &-checkout{
        tbody tr td{
            text-align: left !important;
            line-height: initial !important;
        }
        .product-name{
            a,
            .text--category{
                color: $color__white;
            }
            .text--category{
                // font-size: $fontSize__extra-extra-small;
                // line-height: $fontSize__extra-extra-small + 0.2rem;
                font-size: 1rem;
                margin-bottom: 5px;
            }
            a{
                font-size: $fontSize__default;
                line-height: $fontSize__default + 0.2rem;
                text-transform: none;
            }
        }
        .product-price{
            .price{
                font-size: $fontSize__default;
            }
        }
    }
    &:not(.cart--table-mobile){
        td{
            @include media-breakpoint-down(sm){
                &.product-thumbnail{
                    // display: none;
                }
            }
        }
    }
    &-totals{
        tr{
            th, td{
                font-weight: $fontWeight__semi-bold !important;
            }
        }
    }
    &.cart--table-mobile{
        @include media-breakpoint-down(sm){
            td.show--mobile{
                display: table-cell !important;
            }
            .product-remove.product-price{
                vertical-align: bottom;
                padding-bottom: 26px;
                position: relative;
                a.remove{
                    position: absolute;
                    right: 0;
                    top: 0;
                    padding: 10px 0;
                }
            }
        }
    }
}
.cart--totals{
    margin-top: 15px;
    margin-bottom: 30px;
    .cart--checkout{
        color: $color__white;
        // line-height: 5rem;
        text-transform: uppercase;
        font-weight: $fontWeight__semi-bold;
    }
    h3{
        @extend .title--big;
        color: $color__white;
        font-family: $font__secondary;
        margin-bottom: 5px;
    }
    table{
        margin-bottom: 25px;
        tr{
            td{
                text-align: right;
            }
            th,td{
                font-weight: $fontWeight__default;
                line-height: 5rem;
                text-transform: uppercase;
            }
        }
        .order-total{
            border-top: 1px solid $color__grey;
            th,td{
                font-size: $fontSize__medium;
                font-weight: $fontWeight__semi-bold;
                letter-spacing: 1.29px;
            }
        }
    }
    .promotion{
        display: flex;
        border: 1px solid $color__secondary;
        margin-bottom: 15px;
        input{
            border: 0;
            background-color: transparent;
        }
        #promotion-input{
            flex-grow: 2;
            padding: 0 15px;
        }
        #promotion-submit{
            font-size: $fontSize__small;
            font-weight: $fontWeight__bold;
            padding: 0 15px;
        }
    }
    .checkout-wrapper{
        display: flex;
        .button{
            width: 100%;
            max-width: none;
            justify-content: center;
        }
        & + p{
            margin-top: 20px;
        }
    }
    &-checkout,
    &-default{
        margin: 0;
        background-color: $color__secondary;
        padding: 30px 40px;
        color: $color__white;
        @include media-breakpoint-down(md){
            padding: 30px 15px;
        }
        @include media-breakpoint-down(sm){
            margin: 0 -15px;
            padding: 30px 20px;
        }
        .promotion{
            border-color: $color__white;
            #promotion-submit{
                color: $color__white;
            }
        }
    }
    &-checkout{
        margin-bottom: 30px;
        .product-thumbnail{
            width: 70px;
            max-width: 70px;
        }
        .cart--table-totals{
            border-top: 1px solid $color__grey;
        }
    }
    p{
        a{
            color: $color__white;
        }
    }
}