.search{
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: rgba($color__white, .98);
    z-index: -1;
    transition: all 500ms ease;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    @include media-breakpoint-down(md){
        align-items: flex-start;
    }
    .close{
        position: absolute;
        top: 20px;
        right: 20px;
        i{
            font-size: 4rem;
            color: $color__secondary;
        }
        @include media-breakpoint-down(md){
            top: 10px;
            right: 10px;
        }
    }
    &__content{
        // width: 100%;
        max-height: 100%;
    }
    form{
        // display: flex;
        // justify-content: center;
        // flex-direction: column;
        // align-items: center;
        max-height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        input{
            padding: 10px 60px;
            height: auto;
            font-size: 3.5rem;
            text-align: center;
            border: 0;
            background-color: transparent;
            border-bottom: 1px solid $color__grey--dark;
            @include media-breakpoint-down(md){
                margin-top: 10px;
                font-size: 2.5rem;
                width: 100%;
            }
            @include media-breakpoint-down(sm){
                font-size: 1.5rem;
            }
            &:focus {
                outline:none;
            }
        }
    }
    &.active{
        z-index: 10000;
        opacity: 1;
    }
    .result-search-wrapper{
        display: flex;
        align-items: flex-start;
        flex-wrap: wrap;
        justify-content: center;
        max-width: 980px;
        padding-top: 40px;
        overflow-y: auto;
        max-height: calc( 100vh - 53px);
        @include media-breakpoint-down(md){
            flex-direction: column;
            flex-wrap: nowrap;
            align-items: center;
            justify-content: flex-start;
        }
        .button{
            margin-top: 40px;
            min-height: 40px;
            margin-bottom: 20px;
        }
    }
    .filters{
        width: 100%;
        margin-bottom: 10px !important;
        flex-wrap: wrap;
        li{
            a{
                margin: 0 !important;
            }
        }
    }
    #result-search{
        @extend %list--reset;
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        li{
            width: 50%;
            @include media-breakpoint-down(md){
                width: 100%;
            }
            a{
                display: flex;
                align-items: center;
                border-bottom: 1px solid $color__grey--light;
                // margin: 10px 0;
                padding: 10px;
                transition: background 250ms ease;
                .title{
                    margin: 0;
                    font-family: $font__secondary;
                    font-size: $fontSize__medium;
                    line-height: $fontSize__medium + 0.3rem;
                    font-weight: $fontWeight__semi-bold;
                    display: block;
                    color: $color__secondary;
                    @include media-breakpoint-down(sm){
                        font-size: $fontSize__small;
                        line-height: $fontSize__small + .3rem;
                    }
                }
                p{
                    margin-bottom: 0;
                    opacity: 0.6;
                    color: $color__secondary;
                }
                img{
                    padding-right: 10px;
                    max-width: 80px;
                }
                &:hover{
                    background-color: $color__grey--extra-light;
                }
            }
        }
    }
}